<template>
  <div>
    <p>{{ $translate('instructions.push-notifications') }}</p>
    <div class="text-center">
      <button type="button" @click.stop.prevent="registerPush" class="btn btn-primary">{{ $translate('confirm') }}</button>
    </div>
  </div>
</template>
<script>
import _Services from './_Services.vue';

export default {
  mixins: [_Services],
  methods: {
    registerPush() {
      console.log('clicked registerPush')
      let token = this.$read('user.token');
      console.log(token);
      registerPushDevice(token)
          .then(device => {
            console.log('[registerPushDevice] device:', device);

            if (!device) {
              console.warn("No device returned");
              return;
            }

            let url = endPoints.member.device;
            return this.$api(url, "Device", device)
                .then(response => {
                  console.log("device-success");
                });
          })
          .catch(err => {
            console.error(err);
          })
          .finally(() => {
            stateTagApp.commands.hideModal();
            if(stateTagApp.$read('projects').length === 0){
              stateTagApp.commands.newProject();
            }
          });
    }


  }
}
</script>
