<template>
  <div id="magnifierArea"
       @mousemove="handleMouseMove"
       @mouseenter="handleMouseEnter"
       @mouseleave="handleMouseLeave"
       @touchstart="handleTouchStart"
       @touchmove="handleTouchMove"
       @touchend="handleTouchEnd"
       style="position: relative; touch-action: none;">
    <!-- Spinner -->
    <div class="cam-spinner"></div>
    <!-- Preview image -->
    <img :src="imagePreviewUrl" ref="previewImg" @load="updateAreaDimensions" :class="[limitClass, 'preview-image']"
         :style="previewStyle"/>

    <!-- Mask overlay on preview image -->
    <div class="magnifier-mask" :style="maskStyle"></div>

    <!-- Zoom container showing the magnified area -->
    <div class="magnifier-zoom" :style="zoomStyle">
      <div class="zoom-spinner"></div>
      <img :src="imageUrl" :style="zoomImgStyle"/>
    </div>

    <!-- Zoom Factor Adjuster (only slider guide and dot) -->
    <div class="zoom-adjuster">
      <input type="range" min="0.25" max="1" step="0.01" v-model.number="zoomFactor" class="zoom-slider"/>
    </div>
  </div>
</template>

<script>
import _Services from "@/x-html/_Services";

export default {
  mixins: [_Services],
  props: {
    url: {
      required: false,
      type: String,
      default: ""
    },

    zoomFactor: {
      type: Number,
      default: .25
    },
    zoomWidth: {
      type: String,
      default: "60vw"
    },
    zoomHeight: {
      type: String,
      default: "40vw"
    },
    limitBy: {
      type: String,
      default: 'width', // 'width' or 'height'
      validator: val => ['width', 'height'].includes(val)
    },
    maxWidth: {
      type: String,
      default: "100vw"
    },
    maxHeight: {
      type: String,
      default: "80vh"
    },
  },
  data() {
    return {
      areaWidth: 0,
      areaHeight: 0,
      maskX: 0,
      maskY: 0,
      showZoom: false,
      lastClientX: 0,
      lastClientY: 0,
    };
  },
  computed: {
    imageUrl() {
      return (this.url != "")
          ? this.url
          : this.$read(this.locus);
    },
    // Updated: Prevent .concat() on undefined
    imagePreviewUrl() {
      const base = this.imageUrl;
      if (!base || !this.areaWidth) return base || "";
      return base.concat("?w=" + this.areaWidth);
    },
    zoomWidthPx() {
      return this.parseDimension(this.zoomWidth);
    },
    zoomHeightPx() {
      const raw = this.parseDimension(this.zoomHeight);
      return Math.min(raw, window.innerHeight - 20);
    },
    computedMaskWidth() {
      return this.zoomWidthPx * this.zoomFactor;
    },
    computedMaskHeight() {
      return this.zoomHeightPx * this.zoomFactor;
    },
    maskStyle() {
      return {
        position: 'absolute',
        left: this.maskX + 'px',
        top: this.maskY + 'px',
        width: this.computedMaskWidth + 'px',
        height: this.computedMaskHeight + 'px',
        backgroundColor: 'rgba(0,0,0,0.3)',
        pointerEvents: 'none',
        display: this.showZoom ? 'block' : 'none',
        zIndex: 5
      };
    },
    zoomStyle() {
      // Reverted vertical positioning and flipping logic to use consistent spacing based on mask position
      const desiredSpacing = 12;
      const maskTop = this.lastClientY - this.computedMaskHeight / 2;
      const maskBottom = this.lastClientY + this.computedMaskHeight / 2;
      let topPos;

      if (maskTop >= this.zoomHeightPx + desiredSpacing) {
        topPos = maskTop - this.zoomHeightPx - desiredSpacing;
      } else {
        topPos = maskBottom + desiredSpacing;
      }

      // Clamp to viewport
      if (topPos + this.zoomHeightPx > window.innerHeight) {
        topPos = window.innerHeight - this.zoomHeightPx - 10;
      }

      const leftPos = this.lastClientX - this.zoomWidthPx / 2;
      let adjustedLeft = leftPos;
      if (leftPos < 0) adjustedLeft = 0;
      else if (leftPos + this.zoomWidthPx > window.innerWidth)
        adjustedLeft = window.innerWidth - this.zoomWidthPx;

      return {
        position: 'fixed',
        left: adjustedLeft + 'px',
        top: topPos + 'px',
        width: this.zoomWidthPx + 'px',
        height: this.zoomHeightPx + 'px',
        overflow: 'hidden',
        display: this.showZoom ? 'block' : 'none',
        zIndex: 10
      };
    },
    zoomImgStyle() {
      const effectiveScale = 1 / this.zoomFactor;
      const maskCenterX = this.maskX + this.computedMaskWidth / 2;
      const maskCenterY = this.maskY + this.computedMaskHeight / 2;
      return {
        position: 'absolute',
        width: (this.areaWidth * effectiveScale) + 'px',
        height: (this.areaHeight * effectiveScale) + 'px',
        transform: `translate(${this.zoomWidthPx / 2 - maskCenterX * effectiveScale}px, ${this.zoomHeightPx / 2 - maskCenterY * effectiveScale}px)`
      };
    },
    limitClass() {
      return this.limitBy === 'height' ? 'limit-by-height' : 'limit-by-width';
    },
    previewStyle() {
      return {
        maxHeight: this.maxHeight,
        maxWidth: this.maxWidth
      };
    },
  },
  mounted() {
    this.updateAreaDimensions();
    window.addEventListener('resize', this.updateAreaDimensions);
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.updateAreaDimensions);
  },
  methods: {
    parseDimension(val) {
      if (typeof val !== 'string') return Number(val);
      if (val.endsWith('px')) {
        return parseFloat(val);
      } else if (val.endsWith('vw')) {
        return window.innerWidth * parseFloat(val) / 100;
      } else if (val.endsWith('vh')) {
        return window.innerHeight * parseFloat(val) / 100;
      } else {
        return parseFloat(val);
      }
    },
    updateAreaDimensions() {
      if (this.$refs.previewImg) {
        this.areaWidth = this.$refs.previewImg.offsetWidth;
        this.areaHeight = this.$refs.previewImg.offsetHeight;
      }
    },
    handleMouseEnter() {
      this.showZoom = true;
    },
    handleMouseMove(e) {
      this.lastClientX = e.clientX;
      this.lastClientY = e.clientY;
      const rect = this.$refs.previewImg.getBoundingClientRect();
      let x = e.clientX - rect.left - this.computedMaskWidth / 2;
      let y = e.clientY - rect.top - this.computedMaskHeight / 2;
      if (x < 0) x = 0;
      if (y < 0) y = 0;
      if (x + this.computedMaskWidth > this.areaWidth) {
        x = this.areaWidth - this.computedMaskWidth;
      }
      if (y + this.computedMaskHeight > this.areaHeight) {
        y = this.areaHeight - this.computedMaskHeight;
      }
      this.maskX = x;
      this.maskY = y;
    },
    handleMouseLeave() {
      this.showZoom = false;
    },
    handleTouchStart(e) {
      e.preventDefault();
      this.showZoom = true;
      if (e.touches && e.touches.length > 0) {
        const touch = e.touches[0];
        this.handleMouseMove({clientX: touch.clientX, clientY: touch.clientY});
      }
    },
    handleTouchMove(e) {
      e.preventDefault();
      this.lastClientX = e.clientX;
      this.lastClientY = e.clientY;
      if (e.touches && e.touches.length > 0) {
        const touch = e.touches[0];
        this.handleMouseMove({clientX: touch.clientX, clientY: touch.clientY});
      }
    },
    handleTouchEnd(e) {
      e.preventDefault();
      this.showZoom = false;
    }
  }
};
</script>

<style scoped>
#magnifierArea {
  min-height: 60vh;
}
.limit-by-width {
  width: 100%;
  height: auto;
  display: block;
}

.limit-by-height {
  height: 100vh;
  width: auto;
  display: block;
}
.magnifier-mask {
  border: 1px solid #fff;
}

.magnifier-zoom {
  border: 1px solid #ccc;
  background: #fff;
  border-radius: 10px;
  overflow: hidden;
}

.zoom-adjuster {
  display: none;
  /*display: flex;*/
  position: absolute;
  width: 0px;
  overflow: visible;
  right: 55px;
  top: 50%;
  transform: translateY(-50%);
  align-items: center;
}

.zoom-slider {
  -webkit-appearance: none;
  appearance: none;
  width: 150px;
  height: 8px;
  background: #ddd;
  outline: none;
  opacity: 0.7;
  transition: opacity .2s;
  transform: rotate(-90deg);
  border-radius: 1em;
}

.zoom-slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: #fff;
  border: 2px solid #ccc;
  cursor: pointer;
}

.zoom-slider::-moz-range-thumb {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: #fff;
  border: 2px solid #ccc;
  cursor: pointer;
}

.cam-spinner {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 48px;
  height: 48px;
  border: 5px solid #ccc;
  border-top: 5px solid #333;
  border-radius: 50%;
  animation: spin 0.8s linear infinite;
  pointer-events: none;
}

.zoom-spinner {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 32px;
  height: 32px;
  border: 4px solid #ccc;
  border-top: 4px solid #333;
  border-radius: 50%;
  animation: spin 0.8s linear infinite;

  pointer-events: none;
}

.preview-image {
  position: relative;
  max-height: v-bind(maxHeight);
  max-width: v-bind(maxWidth);
}

@keyframes spin {
  0% { transform: translate(-50%, -50%) rotate(0deg); }
  100% { transform: translate(-50%, -50%) rotate(360deg); }
}
</style>
