<template>
  <select
      id="numberFormatSelect"
      class="form-control"
      v-model="selected"
  >
    <option
        v-for="(format, index) in formats"
        :key="index"
        :value="format.pattern"
    >
<!--      {{ format.pattern }} → -->{{ format.example }}
    </option>
  </select>
</template>

<script>
export default {
  computed: {
    formats() {
      return [
        { pattern: '#,###.##', example: '1,234.56' },
        { pattern: '#.###,##', example: '1.234,56' },
        { pattern: '# ###,##', example: '1 234,56' },
        { pattern: "#'###.##", example: "1'234.56" },
        { pattern: '##,##,###.##', example: '12,34,567.89' },
        { pattern: '######.##', example: '1234567.89' }
      ];
    },
    selected: {
      get() {
        return stateTagApp.$read('langpack.numberFormat');
      },
      set(val) {
        stateTagApp.$write('langpack.numberFormat', val);
      }
    }
  }
};
</script>
