<template>
  <div class="input-group">
    <span v-if="!_.isEmpty(icon)" class="input-group-text">
      <i :class="classes"></i>
    </span>

    <span v-else class="input-group-text">{{ $translate(name) }}</span>

    <input
        :type="type"
        :name="name"
        :placeholder="placeholder"
        class="form-control"
        v-bind:required="required"
        v-model="serviceContent"
        :maxlength="max"
        @change="onChange"
        @blur="onBlur"
    />


  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import _Services from "./_Services"

export default {
  mixins: [_Services],

  props: {
    type: {
      type: String,
      required: true
    },
    name: {
      type: String,
      required: true
    },
    placeholder: {
      type: String,
      required: false
    },
    icon: {
      type: String,
      required: false
    },
    required: {
      type: Boolean,
      default: false
    },
    max: {
      type: Number,
      required: false
    },
  },

  computed: {
    classes() {
      return 'fa fa-'.concat(this.icon)
    }
  },

  data: function() {
    return {
      changed: false,
    }
  },

  methods: {
    onChange(){
      this.changed = true;
    },
    onBlur() {
      if(this.changed){
        this.$emit('updated', {locus: this.locus, service: this.service});
      }
    },
    trace: function () {
      //console.log('traced');
    },
    validate: function () {
      const value = this.serviceContent;

      // Reset validation state
      this.hasError = false;
      this.isValid = false;
      this.errorMessage = "";

      // Required validation
      if (this.required && (!value || value.trim() === "")) {
        this.hasError = true;
        this.errorMessage = "This field is required.";
        return false;
      }

      // Type-specific validation
      if (this.type === "email" && value) {
        const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
        if (!emailRegex.test(value)) {
          this.hasError = true;
          this.errorMessage = "Please enter a valid email address.";
          return false;
        }
      }

      if (this.type === "password" && value) {
        if (value.length < 6) {
          this.hasError = true;
          this.errorMessage = "Password must be at least 6 characters.";
          return false;
        }
      }

      if (this.type === "number" && value) {
        if (isNaN(value)) {
          this.hasError = true;
          this.errorMessage = "Please enter a valid number.";
          return false;
        }
      }

      // If all checks pass
      this.isValid = true;
      return true;
    }
  }
}
</script>

<style scoped>
.input-group {
  margin-bottom: 10px;
}
</style>
