<template>

  <div class="bar-chart-container" ref="container">
    <button class="info-button btn btn-outline-secondary btn-sm" @click=infoModal()>
      <i class="fa fa-info-circle"></i>
    </button>
    <!-- Legend -->
    <div class="legend">
      <div
          v-for="(indicator, index) in indicatorLines"
          :key="'legend-' + index"
          class="legend-item"
      >
        <span
            class="legend-color"
            :style="{
              backgroundColor: indicator.color,
              borderTop: indicator.dasharray !== '0' ? `1px dashed ${indicator.color}` : 'none',
            }"
        ></span>
        <span class="legend-label">{{ indicator.label }}</span>
      </div>
    </div>

    <!-- Y-Axis Ticks and Labels -->
    <div class="y-axis">
      <div
          v-for="(tick, index) in yTicks"
          :key="'y-tick-' + index"
          class="y-tick"
          :style="{ bottom: `${(tick / yMax) * 100}%` }"
      >
        <span class="y-tick-label">{{ Math.round(tick) }}</span>
        <div class="y-tick-line"></div>
      </div>
    </div>

    <!-- X-Axis Labels -->
    <div class="x-axis">
      <div
          v-for="(tick, index) in xTicks"
          :key="'x-label-' + index"
          class="x-label"
          :style="{ left: `${(index / (xTicks.length)) * 100}%` }"
          v-html="tick"
      >

      </div>
    </div>

    <!-- Indicators -->
    <div class="flags">
      <div
          v-for="(indicator, index) in indicatorLines"
          v-if="indicator.value"
          :key="'indicator-' + index"
          class="indicator"
          :style="{
            bottom: `${(indicator.value / yMax) * 100}%`,
            borderTop: `1px dashed ${indicator.color}`,
          }"
      >
        <span
            class="indicator-label"
            :style="{ left: `${indicator.labelX}%`, color: indicator.color }"
        >
          {{ indicator.label }}
        </span>
      </div>
    </div>

    <!-- Bars -->
    <div class="bars">
      <div
          v-for="(bar, index) in bars"
          :key="'bar-' + index"
          class="bar"
          :style="{
            height: `${(bar.amount / yMax) * 100}%`,
            backgroundColor: bar.color,
            left: `${bar.x}%`,
            width: `${bar.width}%`,
          }"
          @mouseenter="showTooltip(bar, $event)"
          @mouseleave="hideTooltip"
          @focus="showTooltip(bar, $event)"
          @blur="hideTooltip"
          tabindex="0"
          :aria-label="`${bar.time}: ${currency}${bar.amount}`"
      >
        <div class="bar-label">{{ bar.time }}</div>
      </div>
    </div>

    <!-- Tooltip -->
    <div
        v-if="tooltip.visible"
        class="tooltip"
        :style="tooltip.style"
    >
      {{ tooltip.content }}
    </div>
  </div>
</template>

<script>
import _ from 'lodash'; // Ensure lodash is imported
import _Services from "@/x-html/_Services";

export default {
  mixins: [_Services],

  props: {
    size: {
      type: String,
      default: '10vh',
    },
    currency: {
      type: String,
      default: '$',
    }
  },
  data() {
    return {
      bars: [],
      indicatorLines: [],
      tooltip: {
        visible: false,
        content: '',
        style: {
          left: '0px',
          top: '0px',
        },
      },
      yMax: 100,
      yTicks: [],
      xTicks: [],
      error: false,
    };
  },
  computed: {
    epoch() {
      return this.$read('epoch');
    },
    projectId() {
      return this.$read('project.id');
    },

    chartData() {
      return this.serviceContent.bars;
    },

    indicators() {
      return this.serviceContent.flags;
    },

    computedSlices() {
      try {
        const dataArray = this.chartData;
        const flagsValues = this.getIndicatorsValues(dataArray);
        const rawYMax = Math.max(
            ...dataArray.map(d => d.amount),
            ...flagsValues
        );
        this.yMax = Math.ceil(rawYMax * 1.1);

        // Define Y-Ticks
        const numberOfTicks = 5;
        const tickStep = this.yMax / numberOfTicks;
        this.yTicks = Array.from({length: numberOfTicks + 1}, (_, i) => i * tickStep);

        // Define Bars
        const totalBars = dataArray.length;
        const gapPercentage = 2;
        const totalGap = gapPercentage * (totalBars - 1);
        const barWidth = (100 - totalGap) / totalBars;
        this.bars = dataArray.map((d, index) => ({
          ...d,
          height: (d.amount / this.yMax) * 100,
          width: barWidth,
          x: index * (barWidth + gapPercentage),
          color: this.getBarColor(d.amount),
        }));

        // Define X-Ticks
        this.xTicks = dataArray.map(d => d.time);

        // Calculate Indicators
        this.calculateIndicators(this.indicators);

        return this.bars;
      } catch (e) {
        console.error("Error computing slices for HtmlBarGraph:", e);
      }
    },
  },
  mounted() {
    this.loadBars();
    window.addEventListener('resize', this.processChartData);
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.processChartData);
  },
  watch: {
    epoch: {
      deep: true,
      handler: 'loadBars',
    },
    projectId(fresh, stale) {
      this.loadBars();
    },
    serviceContent: {
      handler: 'processChartData',
      deep: true,
    }
  },
  methods: {
    infoModal() {
      let title = this.$translate('indicator.lines');
      let body = this.$translate('indicator.info');
      stateTagApp.commands.showModal(title, body);
    },

    loadBars() {
      let url = endPoints.charts.bar.replace('{project}', this.projectId);

      url = this.epochGetString(url);

      this.$api(url, 'BarChart')
          .then(response => {
            stateTagApp.$write('barchart', response.data);
            this.processChartData();
          })
          .catch(err => {
            console.log(err)
          });
    },

    processChartData() {
      try {
        this.computedSlices;
        this.error = false;
      } catch (e) {
        console.error("Error processing chart data:", e);
        this.error = true;
      }
    },

    calculateIndicators(flagsObj) {
      try {
        this.indicatorLines = [
          {
            label: 'Budget',
            value: flagsObj.budget,
            color: stateTagApp.palette[7],
            dasharray: '5,5',
            labelX: 85,
          },
          {
            label: 'Spend',
            value: flagsObj.spend,
            color: stateTagApp.palette[3],
            dasharray: '5,5',
            labelX: 65,
          },
          {
            label: 'Due',
            value: flagsObj.due,
            color: stateTagApp.palette[5],
            dasharray: '2,2',
            labelX: 45,
          },
          {
            label: 'Spend + Due',
            value: flagsObj.spendDue,
            color: stateTagApp.palette[2],
            dasharray: '3,3',
            labelX: 15,
          },
        ];
      } catch (e) {
        console.error("Error calculating flags:", e);
      }
    },

    getIndicatorsValues(dataArray) {
      const flagsObj = this.indicators;
      return [flagsObj.budget, flagsObj.spend, flagsObj.due, flagsObj.spendDue];
    },

    getBarColor(amount) {
      if (amount > 150) return '#FF6384';
      if (amount > 100) return '#36A2EB';
      return '#4BC0C0';
    },

    showTooltip(bar, event) {
      this.tooltip.content = `${this.currency}${bar.amount}`;

      const container = this.$refs.container;
      const containerRect = container.getBoundingClientRect();
      const tooltipWidth = 100;
      const tooltipHeight = 40;

      const barRect = event.target.getBoundingClientRect();
      const left = barRect.left - containerRect.left + barRect.width / 2;
      const top = barRect.top - containerRect.top - tooltipHeight;

      const adjustedLeft = Math.min(Math.max(left, 10), containerRect.width - 10);
      const adjustedTop = Math.max(top, 10);

      this.tooltip.style.left = `${adjustedLeft}px`;
      this.tooltip.style.top = `${adjustedTop}px`;
      this.tooltip.visible = true;
    },

    hideTooltip() {
      this.tooltip.visible = false;
    },
  },
};
</script>

