<template>
  <svg
      v-if="$read('project.budget')"
      :style="{ width: size, height: size }"
      viewBox="0 0 100 100"
      role="img"
      aria-label="Pie chart representing data"
      xmlns="http://www.w3.org/2000/svg"
  >
    <!-- Add a blue ring -->
    <circle
        cx="50"
        cy="50"
        r="48"
        fill="none"
        stroke="#36A2EB"
        stroke-width="4"
    />
    <g>
      <path
          v-for="(slice, index) in computedSlices"
          :key="index"
          :d="describeArc(50, 50, 45, slice.startAngle, slice.endAngle)"
          :fill="slice.color"
      />
    </g>
  </svg>
  <div v-else class="no-budget d-flex align-items-center justify-content-center" :style="{ width: size, height: size }">
    <i class="bi bi-infinity" style="font-size: 6vh; color: #ffb901;"></i>
  </div>
</template>

<script>
import _Services from "@/x-html/_Services";

export default {
  mixins: [_Services],

  props: {
    size: {
      type: String,
      default: '10vh',
    },
  },
  watch: {
    projectId: function (fresh, stale) {
      this.loadPie();
    },

    budget: function (fresh, stale) {
      let url = endPoints.projects.budget
          .replace('{project}', this.projectId);
      this.$api(url, 'ChangeBudget', {budget: fresh}, 'PATCH')
          .then(response => {
            // Update the single project reference
            const project = stateTagApp.$read('project');
            if (project) {
              project.budget = fresh;
              stateTagApp.$write('project', project);
            }

            // Update the project inside the projects list
            const projects = stateTagApp.$read('projects');
            if (Array.isArray(projects)) {
              const index = projects.findIndex(p => p.id === this.projectId);
              if (index !== -1) {
                projects[index].budget = fresh;
                stateTagApp.$write('projects', projects);
              }
            }
          })
          .catch((error) => {
          });
    }
  },
  computed: {
    budget() {
      return stateTagApp.$read('project').budget;
    },
    projectId() {
      return stateTagApp.$read('project.id');
    },
    computedSlices() {
      try {
        const dataArray = this.serviceContent;
        if (!Array.isArray(dataArray) || dataArray.length === 0) {
          console.warn("No chart data provided.");
          return [];
        }
        const total = dataArray.reduce((sum, item) => sum + item.value, 0);
        if (total === 0) {
          console.warn("Total value of chart data is 0.");
          return [];
        }
        let currentAngle = 0;
        const defaultColors = [3, 2, 6].map(i => stateTagApp.palette[i]);


        return dataArray.map((item, index) => {
          const sliceAngle = (item.value / total) * 360;
          const slice = {
            color: item.color || defaultColors[index % defaultColors.length],
            startAngle: currentAngle,
            endAngle: currentAngle + sliceAngle,
          };
          currentAngle += sliceAngle;
          return slice;
        });
      } catch (e) {
        console.error("Error computing slices for PieChart:", e);
        return [];
      }
    },
  },
  mounted: function () {
    this.loadPie();
  },
  methods: {
    loadPie() {
      let endpoint = endPoints.charts.pie.replace('{project}', this.projectId);
      this.$api(endpoint, 'PieChart')
          .then(response => {
            this.$write('pie', response.data)
          }).catch(err => {
      });
    },

    polarToCartesian(cx, cy, r, angleInDegrees) {
      const angleInRadians = ((angleInDegrees - 90) * Math.PI) / 180.0;
      return {
        x: cx + r * Math.cos(angleInRadians),
        y: cy + r * Math.sin(angleInRadians),
      };
    },

    describeArc(cx, cy, r, startAngle, endAngle) {
      const start = this.polarToCartesian(cx, cy, r, endAngle);
      const end = this.polarToCartesian(cx, cy, r, startAngle);
      const largeArcFlag = endAngle - startAngle <= 180 ? "0" : "1";
      const d = [
        "M", cx, cy,
        "L", start.x, start.y,
        "A", r, r, 0, largeArcFlag, 0, end.x, end.y,
        "Z"
      ].join(" ");
      return d;
    },
  },
};
</script>

<style scoped>
svg, .no-budget {
  position: absolute;
  min-width: 2.53em;
  min-height: 2.53em;
  top: 7px;
  left: 7px;
}

.bi-infinity {
  margin-top: 0.7em;
  margin-left: .5em;
}
</style>
