<template>
  <!-- "ref" on the form so we can reference it in resetPassword -->
  <form ref="theForm"
        class="needs-validation"
        novalidate
        @submit.prevent="validateAndSubmitForm($event, submitHandler())">
    <!-- Email Input -->
    <x-input

        service="state"
        locus="user.email"
        name="email"
        type="email"
        icon="user"
        required>
    </x-input>

    <!-- Password Input (only shown if NOT in forgot-password mode) -->
    <x-input v-if="!isForgotPassword"
             service="state"
             locus="user.pw"
             name="password"
             type="password"
             icon="lock"
             required>
    </x-input>

    <!-- Login Actions (only if NOT in forgot-password mode) -->
    <div v-if="!isForgotPassword" class="btn-group w-100" role="group">
      <button @click="forgotPassword" type="button" class="btn btn-secondary">
        {{ $translate('forgot') }}
      </button>
      <button type="submit" class="btn btn-primary">
        {{ $translate('login') }}
      </button>
    </div>

    <!-- Reset/Cancel Actions (only if in forgot-password mode) -->
    <div v-else class="btn-group w-100" role="group">
      <button @click="cancelForgotPassword" type="button" class="btn btn-secondary">
        {{ $translate('login') }}
      </button>

      <button type="submit" class="btn btn-primary">
        {{ $translate('forgot') }}
      </button>
    </div>
  </form>
</template>

<script>
import _Services from "@/x-html/_Services";
import _FormValidation from "@/x-html/_FormValidation";
import state from "@/state";

export default {
  mixins: [_Services, _FormValidation],
  data() {
    return {
      isForgotPassword: false
    };
  },
  methods: {
    submitHandler() {
      if (this.isForgotPassword) {
        return this.resetPassword;
      }
      return this.submitLogin;
    },
    /*
     * Switch to the "forgot password" mode.
     */
    forgotPassword() {
      this.isForgotPassword = true;
    },
    /*
     * Reset the user's password using the parent's validation.
     * - We mock an event object so the parent's `validateAndSubmitForm`
     *   sees the same structure as a normal submit event.
     * - On success, switch back to the login mode.
     */
    resetPassword(formData) {
      // Call your password reset endpoint
      return this.$api(endPoints.password.forgot, "ResetPassword", {email: formData.email})
          .then((response) => {
            // Switch back to login mode
            this.isForgotPassword = false;
            // Inform user the email was sent
            this.$write("errors.modal", response.data.message);
          })
          .catch((error) => {
            // Handle error (e.g., show a message, etc.)
            console.error(error);
          });
    },
    /*
     * Cancel the forgot-password flow, return to login.
     */
    cancelForgotPassword() {
      this.isForgotPassword = false;
    },
    /*
     * Standard login submission (unchanged):
     * This will be called by `validateAndSubmitForm`
     * if all fields are valid in normal login mode.
     */
    submitLogin(formData) {
      return this.$api(endPoints.login, "Login", formData)
          .then((response) => {
            console.log('Member Logged In')
            stateTagApp.$write("user", response.data);
            stateTagApp.commands.hideModal();
            console.log('Notifications: ' + Notification.permission)
            if (Notification.permission != 'granted') {
              let body = '<x-allow-push></x-allow-push>';
              stateTagApp.commands.showModal('', body, {x: false});
            }
          })
          .catch((error) => {
            this.$write("errors.modal", "invalid-login");
          });
    }
  }
};
</script>
