<template>
  <div v-show="active" class="audio-recorder-container">
    <!-- Instruction Message -->
    <div v-if="instruction && !audioURL" class="instruction-message">{{ instruction }}</div>

    <!-- Loading Overlay -->
    <div v-if="loading || countdown" class="loading-overlay">
      <div class="cam-spinner"></div>
      <div class="countdown">{{ countdown }}</div>
    </div>

    <!-- Recording State -->
    <div v-else-if="isRecording" class="recording-overlay">
      <div class="recording-indicator">
        <i class="fas fa-circle-notch fa-spin"></i>
        <span>Recording...</span>
      </div>
      <div class="confirm-controls">
        <button class="icon-btn stop" @click="stopRecording">
          <i class="fas fa-stop"></i>
        </button>
      </div>
    </div>

    <!-- Audio Preview -->
    <div v-else-if="audioURL" class="preview-overlay">
      <audio ref="audio" :src="audioURL" @ended="onAudioEnded" hidden></audio>
      <button class="play-btn" @click="togglePlayback">
        <i v-if="!isPlaying" class="fas fa-play"></i>
        <i v-else class="fas fa-pause"></i>
      </button>
      <div class="confirm-controls" style="position: absolute; bottom: 20px; left: 50%; transform: translateX(-50%); display: flex; gap: 10px;">
        <button class="icon-btn confirm" @click="useAudio" :disabled="isUploading">
          <i v-if="!isUploading" class="fas fa-check"></i>
          <i v-else class="fa fa-spinner spin"></i>
        </button>
        <button class="icon-btn retake" @click="retakeAudio">
          <i class="fas fa-undo"></i>
        </button>
      </div>
    </div>

    <!-- Top Controls -->
    <div class="top-controls" style="z-index: 1050;">
      <div class="control-item" style="margin-left: auto; position: relative;" @click="stopRecording(); $write('nav.microphone', false)">
        <i class="fas fa-times"></i>
      </div>
    </div>
  </div>
</template>

<script>
import _Services from "./_Services.vue";

export default {
  mixins: [_Services],
  data() {
    return {
      isRecording: false,
      mediaRecorder: null,
      audioChunks: [],
      audioBlob: null,
      audioURL: null,
      loading: false,
      isUploading: false,
      error: null,
      countdown: 3,
      countdownInterval: null,
      mimeType: '',
      isPlaying: false,
    };
  },
  computed: {
    instruction: () => stateTagApp.$read('microphone.instruction'),
    endpointUrl: {
      get() {
        return stateTagApp.$read('microphone.endpoint');
      },
      set(value) {
        stateTagApp.$write('microphone.endpoint', value);
      },
    },
    active() {
      return stateTagApp.$read("nav.microphone");
    },
  },
  watch: {
    active(newVal) {
      if (newVal) {
        this.initializeCountdown();
      } else {
        this.clearCountdown();
      }
    },
  },
  methods: {
    initializeCountdown() {
      this.countdown = 3;
      this.audioURL = null;
      this.audioBlob = null;
      this.audioChunks = [];
      this.isRecording = false;
      this.countdownInterval = setInterval(() => {
        if (this.countdown > 0) {
          this.countdown -= 1;
        }
        if (this.countdown === 0) {
          this.startRecording();
          this.clearCountdown();
        }
      }, 1000);
    },
    clearCountdown() {
      if (this.countdownInterval) {
        clearInterval(this.countdownInterval);
        this.countdownInterval = null;
      }
    },
    async startRecording() {
      try {
        this.loading = true;
        const stream = await navigator.mediaDevices.getUserMedia({audio: true});
        // Only WebM recording
        const mimeTypes = ['audio/webm'];
        let selectedType = '';
        for (const type of mimeTypes) {
          if (MediaRecorder.isTypeSupported(type)) {
            selectedType = type;
            break;
          }
        }
        if (!selectedType) {
          throw new Error('No supported mime type for recording.');
        }
        this.mimeType = selectedType;
        this.mediaRecorder = new MediaRecorder(stream, {mimeType: selectedType});
        this.audioChunks = [];

        this.mediaRecorder.ondataavailable = (event) => {
          if (event.data.size > 0) {
            this.audioChunks.push(event.data);
          }
        };

        this.mediaRecorder.onstop = () => {
          this.audioBlob = new Blob(this.audioChunks, {type: this.mimeType});
          this.audioURL = URL.createObjectURL(this.audioBlob);
          stream.getTracks().forEach((track) => track.stop());
        };

        this.mediaRecorder.start();
        this.isRecording = true;
      } catch (err) {
        console.error("Error starting audio recording:", err);
      } finally {
        this.loading = false;
      }
    },
    stopRecording() {
      if (this.mediaRecorder && this.isRecording) {
        this.mediaRecorder.stop();
        this.isRecording = false;
      }
    },
    useAudio() {
      this.isUploading = true;
      if (!this.audioBlob) {
        alert('No audio recorded to use.');
        this.isUploading = false;
        return;
      }
      // Use the correct file extension based on the MIME type
      let extension = 'webm';
      const url = this.endpointUrl.replace('{project}', this.$read('project.id'));
      const formData = new FormData();
      formData.append('file', this.audioBlob, `recording.${extension}`);

      this.$api(url, 'Microphone', formData, undefined, {})
          .then((response) => {
            stateTagApp.$read('ai').activated = 1;;
            let url = endPoints.files.list.replace('{project}', this.$read('project.id'));
            this.$api(url, 'Microphone')
                .then((response) => {
                  if (this.$read('nav.builder')) {
                    this.$write('builder.files', response.data);
                  }
                })
                .catch((error) => {
                  // Optionally handle the error
                });
          })
          .catch((apiError) => {
            console.error('Upload failed:', apiError);
            this.$write('errors.builder', this.$translate('errors.saving'));
          })
          .finally(() => {
            this.isUploading = false;
            if (this.audioURL) {
              URL.revokeObjectURL(this.audioURL);
            }
            this.audioURL = null;
            this.audioBlob = null;
            this.$write('nav.microphone', false);
          });
    },
    retakeAudio() {
      this.resetRecorder();
      this.initializeCountdown();
    },
    resetRecorder() {
      this.isRecording = false;
      this.mediaRecorder = null;
      this.audioChunks = [];
      this.audioBlob = null;
      this.audioURL = null;
      this.isPlaying = false;
      this.clearCountdown();
    },
    togglePlayback() {
      const audioEl = this.$refs.audio;
      if (!this.isPlaying) {
        audioEl.play();
        this.isPlaying = true;
      } else {
        audioEl.pause();
        this.isPlaying = false;
      }
    },
    onAudioEnded() {
      this.isPlaying = false;
    },
  },
  beforeDestroy() {
    this.clearCountdown();
    if (this.mediaRecorder && this.isRecording) {
      this.mediaRecorder.stop();
    }
  },
};
</script>

<style scoped>
audio {
  position: absolute;
  top: -50vh;
}

.countdown {
  color: white;
  position: relative;
  left: -1.25em;
}

.recording-overlay button {
  position: absolute;
  bottom: 0;
}

.instruction-message {
  width: 80%;
  position: absolute;
  top: 25%;
  left: 50%;
  transform: translateX(-50%);
  color: black;
  background-color: white;
  border-radius: 8px;
  padding: 8px 12px;
  z-index: 1100;
}

.play-btn {
  width: 64px;
  height: 64px;
  border-radius: 50%;
  border: none;
  color: rgba(0, 0, 0, 0.6);
  background-color: white;
  font-size: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 1em;
}

</style>
