<template>
  <div v-if="receipts.length" class="receipt-list">
    <transition-group name="receipt" tag="div">
      <div
          v-for="(receipt, index) in receipts"
          :key="receipt.id"
          class="swipe-container"
      >
        <div class="swipe-bg"></div>
        <div v-swipe-delete="() => deleteReceipt(receipt)" class="swipe-foreground">
          <x-receipt :receipt="index" @modified="loadReceipts"></x-receipt>
        </div>
      </div>
    </transition-group>
  </div>
  <div v-else class="alert alert-info">{{ $translate('searches.no-receipts') }}</div>
</template>

<script>
import _Services from "@/x-html/_Services";
import api from "@/api";

export default {
  mixins: [_Services],

  directives: {
    swipeDelete: {
      bind(el, binding) {
        let startX = 0;
        let currentX = 0;
        let deltaX = 0;
        const threshold = 100; // minimum px for swipe left delete
        el.style.transition = "transform 0.3s ease";

        const touchStartHandler = (e) => {
          startX = e.touches[0].clientX;
          el.style.transition = ""; // remove transition during drag
        };

        const touchMoveHandler = (e) => {
          currentX = e.touches[0].clientX;
          deltaX = currentX - startX;
          if (deltaX < 0) {
            el.style.transform = `translateX(${deltaX}px)`;
          }
        };

        const touchEndHandler = () => {
          if (deltaX < -threshold) {
            el.style.transition = "transform 0.3s ease";
            el.style.transform = "translateX(-100%)";
            setTimeout(() => {
              binding.value();
            }, 300);
          } else {
            el.style.transition = "transform 0.3s ease";
            el.style.transform = "translateX(0)";
          }
        };

        el.addEventListener("touchstart", touchStartHandler);
        el.addEventListener("touchmove", touchMoveHandler);
        el.addEventListener("touchend", touchEndHandler);

        el._swipeDeleteHandlers = {touchStartHandler, touchMoveHandler, touchEndHandler};
      },
      unbind(el) {
        const handlers = el._swipeDeleteHandlers;
        if (handlers) {
          el.removeEventListener("touchstart", handlers.touchStartHandler);
          el.removeEventListener("touchmove", handlers.touchMoveHandler);
          el.removeEventListener("touchend", handlers.touchEndHandler);
        }
      }
    }
  },

  computed: {
    epoch() {
      return this.$read("epoch");
    },
    receipts() {
      return this.$read("receipts"); // reading from your store
    },
    projectId() {
      return this.$read("project").id;
    }
  },

  methods: {
    deleteReceipt(receipt) {
      const originalReceipts = [...this.receipts];
      const updatedReceipts = this.receipts.filter(r => r.id !== receipt.id);
      this.$write("receipts", updatedReceipts);

      let url = endPoints.receipts.delete
          .replace("{project}", this.projectId)
          .replace("{receipt}", receipt.id)
          .concat('?result-set=').concat(receipt.resultSet);

      this.$api(url, "ReceiptsWithUndo".concat(receipt.id), undefined, "DELETE")
          .then(() => {
            // Optionally re-sync from the server.
          })
          .catch((error) => {
            //console.error(error);
            this.$write("receipts", originalReceipts);
          });
    },

    loadReceipts() {
      let url = endPoints.receipts.list.replace("{project}", this.projectId);
      url = this.epochGetString(url);

      this.$api(url, "Receipts")
          .then((response) => {
            this.$write("receipts", response.data);
            // After loading and writing, check if notification should trigger expansion
            const fresh = stateTagApp.$read('notification');
            if (fresh?.model === 'statement') {
              const match = response.data.find(r => r.id === fresh.id);
              if (match) this.$forceUpdate(); // triggers Receipt.vue's mounted() to run again
            }
          })
          .catch((error) => {
            //console.error(error);
          });
    }
  },

  watch: {
    epoch: {
      deep: true,
      handler: "loadReceipts"
    },
    projectId(newVal, oldVal) {
      this.loadReceipts();
    },
    notification: {
      handler(fresh) {
        if (!fresh.id
            || fresh.model != 'statement'
            || fresh.project != this.projectId) {
          return;
        }

        const match = this.receipts.find(r => r.id === fresh.id);
        if (!match) {
          this.loadReceipts();
        }
      },
      deep: false,
      immediate: false
    }
  },

  mounted() {
    this.loadReceipts();
    this.$onSocket("ProjectTouched", function (event, payload) {
      payload = (payload && payload.message) || {};
      if (payload.project == this.projectId) {
        this.loadReceipts();
      }
    });
  }
};
</script>

<style>
.receipt-list {
  position: relative;
  z-index: 0; /* Ensure receipts are below dropdowns */
}

.swipe-container {
  position: relative;
  overflow: hidden;
}

.swipe-bg {
  background-color: #ffcccc; /* Light red background */
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.swipe-foreground {
  position: relative;
  z-index: 1;
}

x-receipt:first-of-type .receipt-card {
  border-top: 1px solid #cccccc;
}

.receipt-enter-active, .receipt-leave-active {
  transition: all 0.3s ease;
}

.receipt-enter, .receipt-leave-to {
  opacity: 0;
  transform: translateY(20px);
}

.receipt-move {
  transition: transform 0.3s ease;
}

/* Added styles for receipt-card-header for touch improvement */
.receipt-card-header {
  background-color: #fdfdfd;
  padding: 1rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 0.5rem;
  overflow: hidden;
  max-height: 80px;
  transition: max-height 0.35s ease, padding 0.35s ease;
}

.receipt-card-header.header-hidden {
  max-height: 0;
  padding-top: 0;
  padding-bottom: 0;
  pointer-events: none;
}
</style>
