<template>
  <!-- Form with "ref" for reference -->
  <form ref="theForm"
        class="needs-validation text-center"
        novalidate
        @submit.prevent="validateAndSubmitForm($event, submitForm)">
    <!-- Email Input -->
    <div class="mb-3">
      <h4 class="d-flex justify-content-center align-items-center">
        <i class="fas fa-user me-2"></i>
        <x-text-html locus="passwordReset.email"></x-text-html>
      </h4>
    </div>

    <!-- Password Input Fields -->
    <div class="mb-3">
      <x-input
          locus="passwordReset.password"
          name="password"
          type="password"
          icon="lock"
          required>
      </x-input>
    </div>

    <div class="mb-3">
      <x-input
          locus="passwordReset.password_confirmation"
          name="password_confirmation"
          type="password"
          icon="lock"
          required>
      </x-input>
    </div>

    <!-- Submit Button -->
    <div class="d-flex justify-content-center">
      <div class="btn-group">
        <button @click="gotoLogin" type="button" class="btn btn-secondary">
          {{ $translate('login') }}
        </button>
        <button type="submit" class="btn btn-primary">
          {{ $translate('submit') }}
        </button>
      </div>
    </div>
  </form>
</template>

<script>
import _Services from "@/x-html/_Services";
import _FormValidation from "@/x-html/_FormValidation";

export default {
  mixins: [_Services, _FormValidation],
  methods: {
    gotoLogin(){
      stateTagApp.commands.clear('nav');
      stateTagApp.commands.clear('passwordReset');
      stateTagApp.commands.showLogin();
    },
    submitForm() {
      let json = stateTagApp.$read('passwordReset');
      this.$api(endPoints.password.reset, 'ResetPassword', json)
          .then(response => {
            stateTagApp.commands.clear('nav.passwordReset');
            this.$write("user", response.data);
            this.$broadcast({from: "Login", type: "api", event: "success"});
            this.$router.push({ name: 'home' });
          })
          .catch(error => {
            if (error.response && error.response.data.errors) {
              let errors = error.response.data.errors;
              let errorMessages = Object.values(errors).flat();
              let formattedErrors = errorMessages.map(msg => `- ${msg}`).join("\n");
              stateTagApp.$write('errors.modal', formattedErrors);
            } else {
              stateTagApp.$write('errors.modal', "- An unknown error occurred.");
            }
          });
    }
  }
};
</script>
