import _ from "lodash";
import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";
import { cacheAdapterEnhancer } from 'axios-extensions';
import "nprogress/nprogress.css";
import NProgress from "nprogress";

var subdomain = window.location.hostname.split('.')[0];
var currentEnv = subdomain || process.env.NODE_ENV;

stateTagApp['api'] = stateTagApp['api'][currentEnv];
console.log('Connected to API: ', stateTagApp['api']);

const requests = {};

NProgress.configure({
    parent: "body",
    showSpinner: true,
    trickleRate: 0.1,
    trickleSpeed: 400
});

let count = 0;

// Add a request interceptor
axios.interceptors.request.use(
    function (config) {

        // Add a unique timestamp to the query params
        //config.params = config.params || {};
        //config.params["_t"] = Date.now();

        let data = {
            type: "animation",
            from: "api",
            event: 'unknown',
            app: "stateTagApp"
        };

        if (!count) {
            NProgress.start();
            data.event = 'start';
            stateTagApp.$broadcast(data);
        } else if (count === 1) {
            NProgress.done(true);
            NProgress.configure({ parent: "body" });
            NProgress.start();
        }
        count++;
        if (count !== 1) {
            data.event = 'increment';
        }

        return config;
    },
    function (error) {
        return Promise.reject(error);
    }
);

axios.interceptors.response.use(
    function (response) {
        let data = {
            type: "animation",
            from: "api",
            event: 'unknown',
            app: "stateTagApp"
        };

        count--;
        data.event = 'decrement';

        if (!count) {
            data.event = 'stop';
            stateTagApp.$broadcast(data);
            NProgress.done(true);
        }
        return response;
    },
    function (error) {
        let data = {
            type: "animation",
            from: "api",
            event: 'unknown',
            app: "stateTagApp"
        };

        count--;
        data.event = 'decrement';

        if (!count) {
            NProgress.done(true);
            data.event = 'stop';
            stateTagApp.$broadcast(data);
        }
        return Promise.reject(error);
    }
);

// Disable caching in cacheAdapterEnhancer
axios.defaults.adapter = cacheAdapterEnhancer(axios.defaults.adapter, {
    enabledByDefault: false
});

Vue.use(VueAxios, axios);

export default {
    install(Vue, opts) {
        Vue.mixin({
            methods: {
                $api: function (endpoint, caller, post, method, headers = {}) {
                    //console.log(caller);

                    if(endpoint.includes('/null')){
                        //console.log('null endpoint');
                        return Promise.reject();
                    }

                    let config = {};
                    //console.log(isSecureEndpoint(endpoint));
                    if (isSecureEndpoint(endpoint)) {
                        let token = stateTagApp.$read('user.token');
                        headers['Authorization'] = 'Bearer ' + token;
                    }

                    endpoint = stateTagApp.api.concat('/').concat(endpoint);

                    caller = caller.concat('-').concat(this._uid);
                    let cancelToken = this.privateAutoCancel(caller, this._uid);

                    config = { ...config, headers, cancelToken };

                    method = _.isUndefined(method)
                        ? (_.isUndefined(post) ? 'get' : 'post')
                        : method.toLowerCase();

                    if (caller.includes('WithUndo')) {
                        // Abortable, delayed call
                        const timeoutSec = stateTagApp.$read("sta.undo.delay");
                        return new Promise((resolve, reject) => {
                            // Expose reject so Undo.vue can trigger the catch
                            stateTagApp.$write("sta.undo.reject", reject);
                            const timer = setTimeout(() => {
                                // Clear undo UI
                                stateTagApp.$write('sta.undo.timeoutId', null);

                                // Clean up reject when timer fires
                                stateTagApp.$write("sta.undo.reject", null);

                                // Perform the actual request
                                const req = _.isUndefined(post)
                                    ? axios[method](endpoint, config)
                                    : axios[method](endpoint, post, config);
                                req.then(response => {
                                    stateTagApp.$broadcast({
                                        type: 'api',
                                        from: caller.split('-')[0],
                                        event: response.status,
                                        message: 'success'
                                    });
                                    resolve(response);
                                }).catch(error => {
                                    try {
                                        stateTagApp.$broadcast({
                                            type: 'api',
                                            from: caller.split('-')[0],
                                            event: error.response.status,
                                            message: error.response.data.message
                                        });
                                    } catch (e) {
                                        // canceled or other error
                                    }
                                    reject(error);
                                });
                            }, timeoutSec * 1000);
                            // Save timer so Undo.vue can clear it
                            stateTagApp.$write("sta.undo.timeoutId", timer);
                        });
                    } else {
                        // Regular immediate call
                        const promise = _.isUndefined(post)
                            ? axios[method](endpoint, config)
                            : axios[method](endpoint, post, config);
                        promise.then(response => {
                            stateTagApp.$broadcast({
                                type: 'api',
                                from: caller.split('-')[0],
                                event: response.status,
                                message: 'success'
                            });
                        }).catch(error => {
                            try {
                                stateTagApp.$broadcast({
                                    type: 'api',
                                    from: caller.split('-')[0],
                                    event: error.response.status,
                                    message: error.response.data.message
                                });
                            } catch (e) {
                                // probably cancelled
                            }
                        });
                        return promise;
                    }
                },

                privateAutoCancel: function (caller, uid) {
                    let key = caller + '-' + uid;

                    if (!_.isEmpty(requests[key])) {
                        requests[key].cancel();
                    }

                    let cancel = axios.CancelToken.source();
                    requests[key] = cancel;
                    return cancel.token;
                }
            }
        });
    }
};
