<template>
  <div>
    <div class="input-group">
      <span class="input-group-text">
        <i class="fa fa-search"></i>
      </span>
      <input
          type="text"
          :id="id"
          class="form-control"
          v-model="query"
          @input="fetchResults"
          :placeholder="translatePlaceholder()"
          autocomplete="off"
      />
    </div>
  </div>
</template>


<script>
export default {
  props: {
    url: {
      type: String,
      required: true,
    },
    placeholder: {
      type: String,
      required: true
    },
    id: {
      type: String,
      default: "searchInput",
    },
    icon: {
      type: String,
      default: null, // Class name for the icon
    },
    minQueryLength: {
      type: Number,
      default: 5, // Minimum characters required to trigger query
    }
  },
  data() {
    return {
      query: "",
    };
  },
  computed: {
    cancellationKey() {
      // Generate a unique key for each instance of the component to ensure that
      // cancellations are not shared between instances.
      return  this.id;
     },
   },
  methods: {
    translatePlaceholder() {
      return this.$translate(this.placeholder);
    },
    fetchResults() {
      if (this.query.trim().length < this.minQueryLength) {
        this.$emit("resultsUpdated", []); // Emit empty array if input length is too short
        return;
      }

      let url = eval(this.url).replace("{query}", encodeURIComponent(this.query));

      this.$api(url, this.id)
          .then((response) => {
            this.$emit("resultsUpdated", response.data); // Emit the result list on HTTP 200
          })
          .catch((error) => {
            console.error("Error fetching results:", error);
            //this.$emit("resultsUpdated", []); // Emit empty array on error
          });
    },
  },
};
</script>

