<template>
  <div v-if="!isPortrait" class="carousel-overlay">
    <div class="carousel-container">
      <div class="carousel-track">
        <div
          v-for="(src, i) in images"
          :key="i"
          class="carousel-item"
        >
          <img :src="glideSrc(src)" alt="Image" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import _Services from "@/x-html/_Services";
  export default {
    mixins: [_Services],
    data() {
      return {
        isPortrait: window.innerHeight > window.innerWidth
      };
    },
    computed: {
      images: function () {
        const receipts = this.$read('receipts') || [];
        return receipts
          .filter(r => r && r.photo)
          .map(r => r.photo);
      }
    },
    mounted() {
      window.addEventListener("resize", this.updateOrientation);
    },
    beforeDestroy() {
      window.removeEventListener("resize", this.updateOrientation);
    },
    methods: {
      glideSrc: function (src){
        return src.concat('?h=250');
      },

      updateOrientation() {
        this.isPortrait = window.innerHeight > window.innerWidth;
      }
    }
  };
</script>

<style scoped>
.carousel-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(255, 255, 255, 0.98);
  z-index: 9999;
  display: flex;
  align-items: center;
  justify-content: center;
}
.carousel-container {
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  padding: 20px 0;
}

.carousel-track {
  display: flex;
  gap: 20px;
  padding: 0 20px;
}

.carousel-item {
  flex: 0 0 auto;
  width: 200px;
  perspective: 1000px;
  transform-style: preserve-3d;
  transition: transform 0.3s;
}

.carousel-item img {
  width: 100%;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
}
</style>
