<template>
  <div v-show="active" class="camera-container" :class="{ landscape: isLandscape }">
    <div v-if="loading" class="loading-overlay">
      <div class="cam-spinner"></div>
      <div class="camera-error-msg">{{ $translate('instructions.restart-device') }}</div>
    </div>

    <video
        v-show="!showConfirm && !loading"
        autoplay
        muted
        playsinline
        webkit-playsinline
        style="width: 100%; height: 100%; object-fit: cover;"
        class="media-view"
    ></video>
    <div class="framing-overlay"></div>
    <div
      v-show="showConfirm && !loading"
      ref="magnifierArea"
      class="magnifier-area"
      @mousemove="handleMouseMove"
      @mouseenter="handleMouseEnter"
      @mouseleave="handleMouseLeave"
      @touchstart.prevent="handleTouchStart"
      @touchmove.prevent="handleTouchMove"
      @touchend.prevent="handleTouchEnd"
      style="position: relative; width: 100%; height: 100%; touch-action: none;"
    >
      <img ref="previewImg" :src="currentImageData" class="media-view" />
      <div class="magnifier-mask" :style="maskStyle"></div>
      <div class="magnifier-zoom" :style="zoomStyle">
        <img :src="fullResData || currentImageData" :style="zoomImgStyle" />
      </div>
      <div class="zoom-adjuster">
        <input
          type="range"
          min="1"
          max="4"
          step="0.05"
          v-model.number="zoomFactor"
          class="zoom-slider"
        />
      </div>
    </div>
    <div v-if="showConfirm && fileInfo.size" class="file-info-panel">
      {{ fileInfo.width }}×{{ fileInfo.height }} • {{ (fileInfo.size / 1024).toFixed(1) }} KB
    </div>

    <div class="top-controls">
      <div
        v-for="(batch, index) in activeBatchIds"
        :key="'batch-' + (batch === null ? 'global' : batch)"
        class="upload-progress-wrapper"
        :style="{ bottom: `${index * 5}px` }"
      >
        <div class="upload-progress-fill" style="width: 100%;"></div>
      </div>
      <div class="control-item orientation-toggle" @click="toggleOrientation">
        <i class="fas fa-sync-alt"></i>
      </div>
      <div style="width: 100px; text-align: center;">
        <small v-if="!uploadingInProgress">{{ $translate('camera') }} v{{ cameraVersion }}</small>
        <small v-else>{{ uploadQueueTotal - uploadQueue.length }} / {{ uploadQueueTotal }}</small>
      </div>
      <div class="control-item auto-toggle" @click="toggleAutoMode">
        <template v-if="autoApproveDurations[selectedAutoApproveIndex] === 0 || (!autoCaptureCountdownTimer && !autoApproveCountdownTimer)">
          <i class="fas fa-stopwatch" :style="autoApproveDurations[selectedAutoApproveIndex] !== 0 ? 'filter: drop-shadow(0 0 4px white);' : ''"></i>
          <small v-show="autoApproveDurations[selectedAutoApproveIndex] !== 0">
            {{ (autoApproveDurations[selectedAutoApproveIndex] / 1000) + 's' }}
          </small>
        </template>
        <template v-else>
          <i class="fas fa-stop" style="color: red;"></i>
        </template>
      </div>
      <div class="control-item batch-toggle" @click="toggleBatchMode">
        <i :class="isBatchMode ? 'fas fa-images' : 'fas fa-image'" :style="isBatchMode ? 'filter: drop-shadow(0 0 4px white);' : ''"></i>
      </div>
      <div class="control-item close-camera-btn" @click="closeCamera">
        <i class="fas fa-times"></i>
      </div>
    </div>

    <div class="controls">
      <div class="capture-controls" v-show="!showConfirm && !loading">
        <button class="icon-btn capture" :disabled="isCapturing" @click="startAutoCaptureCountdown">
          <i v-if="!isCapturing && autoApproveDurations[selectedAutoApproveIndex] === 0" class="fas fa-camera"></i>
          <i v-if="!isCapturing && autoApproveDurations[selectedAutoApproveIndex] !== 0" class="fas timer">
            {{ Math.ceil(autoCaptureRemaining / 1000) || (autoApproveDurations[selectedAutoApproveIndex] / 1000) }}
          </i>
          <i v-else-if="isCapturing" class="fa fa-spinner spin"></i>
        </button>
      </div>
      <div class="confirm-controls" v-show="showConfirm && !loading">
        <!-- Currency Code Dropdown -->
        <div class="camera-dropdown" v-if="$read('camera.currency')">
          <button style="line-height: 1em;" class="icon-btn dropdown-toggle" @click.stop="toggleDropdown">
            {{ selectedCurrency }}
          </button>
          <div v-show="dropdownVisible" class="camera-dropdown-menu">
            <ul>
              <li v-for="currency in $read('currencies.options')" :key="currency" @click="selectDropdownOption(currency)">
                {{ currency }}
              </li>
            </ul>
          </div>
        </div>
        <button class="icon-btn confirm" @click="usePhoto">
          <i v-if="!autoApproveRemaining" class="fas fa-check"></i>
          <i class="fas timer" v-else>{{ Math.ceil(autoApproveRemaining / 1000) }}</i>
        </button>
        <button class="icon-btn retake" @click="retakePhoto">
          <i class="fas fa-undo"></i>
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { nextTick } from 'vue';
import _Services from "./_Services.vue";

// ---- IndexedDB settings for persistent multi‑item upload queue ----
const DB_NAME      = 'cameraUploads';
const DB_VERSION   = 1;
const STORE_QUEUE  = 'queue';

export default {
  mixins: [_Services],
  props: {
    resolutionFactor: {
      type: Number,
      default: 100
    },
    zoomFactor: { type: Number, default: 2 },
    zoomWidth:  { type: String, default: "150px" },
    zoomHeight: { type: String, default: "150px" },
    cameraVersion: {
      type: String,
      default: '7.1.1'
    },
  },
  data() {
    return {
      autoCaptureRemaining: 0,
      autoCaptureCountdownTimer: null,
      currentImageData: null,
      showConfirm: false,
      videoElement: null,
      stream: null,
      loading: false,
      autoApproveTimer: null,
      cameraHealthCheckTimer: null,
      autoApproveDurations: [0, 3000, 5000, 7000, 10000],
      captureCanvas: null,
      previewCanvas: null,
      fullCanvas: null,
      imageCapture: null,
      dropdownVisible: false,
      isCapturing: false,
      fileInfo: {
        width: 0,
        height: 0,
        size: 0
      },
      fullResData: null,
      areaWidth:  0,
      areaHeight: 0,
      maskX:      0,
      maskY:      0,
      showZoom:   false,
      // Add upload queue and progress flag for background uploads
      uploadQueue: [],
      uploadingInProgress: false,
      uploadProgress: 0,
      uploadQueueTotal: 0,
      autoApproveRemaining: 0,
      autoApproveCountdownTimer: null,
      isBatchMode: false,
      batches: [],
    };
  },
  computed: {
    activeBatchIds() {
      // unique batch IDs; ensure `null` is present while an upload is active
      const ids = this.uploadQueue.map(j => j.batch);
      if (this.uploadingInProgress && !ids.includes(null)) {
        ids.push(null); // keep global bar visible even when queue is momentarily empty
      }
      return [...new Set(ids)];
    },

    zoomWidthPx() {
    /* Derived list of batch IDs that still have queued jobs */
      return this.parseDimension(this.zoomWidth);
    },
    zoomHeightPx() {
      return this.parseDimension(this.zoomHeight);
    },
    computedMaskWidth() {
      return this.zoomWidthPx / this.zoomFactor;
    },
    computedMaskHeight() {
      return this.zoomHeightPx / this.zoomFactor;
    },
    maskStyle() {
      return {
        position: "absolute",
        left:     this.maskX + "px",
        top:      this.maskY + "px",
        width:    this.computedMaskWidth + "px",
        height:   this.computedMaskHeight + "px",
        backgroundColor:"rgba(0,0,0,0.3)",
        pointerEvents:  "none",
        display:        this.showZoom ? "block" : "none",
        zIndex:         20
      };
    },
    zoomStyle() {
      return {
        position: "absolute",
        left:     (this.maskX + this.computedMaskWidth/2 - this.zoomWidthPx/2) + "px",
        top:      (this.maskY - this.zoomHeightPx - 10) + "px",
        width:    this.zoomWidthPx + "px",
        height:   this.zoomHeightPx + "px",
        overflow: "hidden",
        display:  this.showZoom ? "block" : "none",
        zIndex:   20
      };
    },
    zoomImgStyle() {
      const scale      = this.zoomFactor;
      const cx         = this.maskX + this.computedMaskWidth/2;
      const cy         = this.maskY + this.computedMaskHeight/2;
      return {
        position: "absolute",
        width:    (this.areaWidth * scale) + "px",
        height:   (this.areaHeight * scale) + "px",
        transform:`translate(${this.zoomWidthPx/2 - cx*scale}px, ${this.zoomHeightPx/2 - cy*scale}px)`
      };
    },
    selectedCurrency: {
      get(){
        return stateTagApp.$read('currencies.selected');
      },
      set(value){
        stateTagApp.$write('currencies.selected', value);
      }
    },

    // Persisted Property: isLandscape
    isLandscape: {
      get() {
        return stateTagApp.$read('camera.landscape');
      },
      set(value) {
        stateTagApp.$write('camera.landscape', value);
      },
    },
    // Persisted Property: selectedAutoApproveIndex
    selectedAutoApproveIndex: {
      get() {
        return stateTagApp.$read('camera.auto');
      },
      set(value) {
        stateTagApp.$write('camera.auto', value);
      },
    },
    // Persisted Property: endpointUrl
    endpointUrl: {
      get() {
        return stateTagApp.$read('camera.endpoint');
      },
      set(value) {
        stateTagApp.$write('camera.endpoint', value);
      },
    },
    // Persisted Property: batchId
    batchId: {
      get()  { return stateTagApp.$read('camera.batch'); },
      set(v) { stateTagApp.$write('camera.batch', v); }
    },
    // Active State
    active() {
      return stateTagApp.$read('nav.camera');
    },
  },
  watch: {
    active(val, oldVal) {
      if (!oldVal && val) {
        this.startCamera();
      } else if (oldVal && !val) {
        this.stopCamera();
      }
    },
  },

  mounted() {
    this.setupMenu().then(() => {
      if(this.locus != 'currencies.selected'){
        this.$write(this.locus, this.$read('currencies.selected'));
      }
    }) ;

    this.videoElement = this.$el.querySelector('video');
    this.captureCanvas = document.createElement('canvas');
    this.fullCanvas = document.createElement('canvas');
    this.previewCanvas = document.createElement('canvas');
    document.addEventListener('click', this.handleClickOutside);
    if (this.active) {
      this.startCamera();
    }
    this.updateAreaDimensions();
    window.addEventListener("resize", this.updateAreaDimensions);
    // Reload any persisted uploads
    this.loadQueuedJobs();
    if (this.batchId) {
      this._enqueueProcessBatchJob();
    }
  },
  beforeDestroy() {
    document.removeEventListener('click', this.handleClickOutside);
    window.removeEventListener("resize", this.updateAreaDimensions);
  },
  methods: {
    /* ──────────────────────────────────────────────────────────────
     *  IndexedDB helpers – persist an arbitrary-length upload queue
     * ──────────────────────────────────────────────────────────────*/
    openDB() {
      return new Promise((resolve, reject) => {
        const req = indexedDB.open(DB_NAME, DB_VERSION);
        req.onupgradeneeded = e => {
          const db = e.target.result;
          if (!db.objectStoreNames.contains(STORE_QUEUE)) {
            db.createObjectStore(STORE_QUEUE, { keyPath: 'id' });
          }
        };
        req.onsuccess = e => resolve(e.target.result);
        req.onerror   = e => reject(e.target.error);
      });
    },

    async saveQueuedJob(record) {
      const db = await this.openDB();
      return new Promise(res => {
        const tx = db.transaction(STORE_QUEUE, 'readwrite');
        tx.objectStore(STORE_QUEUE).put(record);
        tx.oncomplete = () => res();
      });
    },

    async deleteQueuedJob(id) {
      const db = await this.openDB();
      return new Promise(res => {
        const tx = db.transaction(STORE_QUEUE, 'readwrite');
        tx.objectStore(STORE_QUEUE).delete(id);
        tx.oncomplete = () => res();
      });
    },

    async loadQueuedJobs() {
      const db = await this.openDB();
      return new Promise(res => {
        const jobs = [];
        const tx   = db.transaction(STORE_QUEUE, 'readonly');
        const store = tx.objectStore(STORE_QUEUE);
        store.openCursor().onsuccess = e => {
          const cursor = e.target.result;
          if (cursor) {
            jobs.push(cursor.value);
            cursor.continue();
          } else {
            res(jobs);
          }
        };
        store.openCursor().onerror = () => res(jobs);
      }).then(async records => {
        // recreate Canvas objects from stored dataURLs
        for (const r of records) {
          // If this is a processBatch job, push directly and continue
          if (r.type === 'processBatch') {
            this.uploadQueue.push(r);
            continue;
          }
          // avoid duplicates on hot‑reload: only add if missing
          if (!this.uploadQueue.some(j => j.id === r.id)) {
            const img = await new Promise(ok => {
              const i = new Image();
              i.onload = () => ok(i);
              i.src = r.dataURL;
            });
            const canvas = document.createElement('canvas');
            canvas.width = img.width;
            canvas.height = img.height;
            canvas.getContext('2d').drawImage(img, 0, 0);
            this.uploadQueue.push({
              id: r.id,
              canvas,
              currency: r.currency,
              endpoint: r.endpoint,
              batch: r.batch
            });
          }
        }
        this.uploadQueueTotal = this.uploadQueue.length;
        if (this.uploadQueue.length && !this.uploadingInProgress) {
          this.processUploadQueue();
        }
      });
    },

    startAutoCaptureCountdown() {
      this.clearAutoCaptureCountdown();
      const delay = this.autoApproveDurations[this.selectedAutoApproveIndex];
      if (delay > 0) {
        this.autoCaptureRemaining = delay;
        this.autoCaptureCountdownTimer = setInterval(() => {
          this.autoCaptureRemaining -= 250;
          if (this.autoCaptureRemaining <= 0) {
            this.clearAutoCaptureCountdown();
            this.capturePhoto();
          }
        }, 250);
      } else {
        this.capturePhoto(); // fallback for manual mode
      }
    },
    clearAutoCaptureCountdown() {
      if (this.autoCaptureCountdownTimer) {
        clearInterval(this.autoCaptureCountdownTimer);
        this.autoCaptureCountdownTimer = null;
      }
      this.autoCaptureRemaining = 0;
    },
    parseDimension(val) {
      if (typeof val !== "string") return Number(val);
      if (val.endsWith("px")) return parseFloat(val);
      if (val.endsWith("vw")) return window.innerWidth * parseFloat(val)/100;
      if (val.endsWith("vh")) return window.innerHeight * parseFloat(val)/100;
      return parseFloat(val);
    },
    updateAreaDimensions() {
      if (this.$refs.previewImg) {
        this.areaWidth  = this.$refs.previewImg.offsetWidth;
        this.areaHeight = this.$refs.previewImg.offsetHeight;
      }
    },
    handleMouseEnter() {
      this.showZoom = true;
      this.updateAreaDimensions();
    },
    handleMouseMove(e) {
      if (!this.showZoom) return;
      const rect = this.$refs.previewImg.getBoundingClientRect();
      let x = e.clientX - rect.left - this.computedMaskWidth/2;
      let y = e.clientY - rect.top  - this.computedMaskHeight/2;
      this.maskX = Math.max(0, Math.min(x, this.areaWidth - this.computedMaskWidth));
      this.maskY = Math.max(0, Math.min(y, this.areaHeight - this.computedMaskHeight));
    },
    handleMouseLeave() {
      this.showZoom = false;
    },
    handleTouchStart(e) {
      e.preventDefault();
      this.showZoom = true;
      this.updateAreaDimensions();
      if (e.touches && e.touches[0]) {
        this.handleMouseMove({ clientX: e.touches[0].clientX, clientY: e.touches[0].clientY });
      }
    },
    handleTouchMove(e) {
      e.preventDefault();
      if (e.touches && e.touches[0]) {
        this.handleMouseMove({ clientX: e.touches[0].clientX, clientY: e.touches[0].clientY });
      }
    },
    handleTouchEnd(e) {
      e.preventDefault();
      this.showZoom = false;
    },
    setupMenu: function () {
      return this.$api(endPoints.currencies, 'setupCurrencyMenu')
          .then(result => {
            this.$write('currencies.options', result.data);
          });
    },

    handleClickOutside(event) {
      const dropdown = this.$el.querySelector('.camera-dropdown');
      if (dropdown && !dropdown.contains(event.target)) {
        this.dropdownVisible = false;
      }
    },
    toggleDropdown() {
      this.dropdownVisible = !this.dropdownVisible;
    },
    selectDropdownOption(currency) {
      this.selectedCurrency = currency;
      this.dropdownVisible = false;
      stateTagApp.$write('currencies.selected', currency);
      //console.log('Selected currency:', currency);
    },
    async startCamera() {
      this.loading = true;
      try {
        this.stream = await navigator.mediaDevices.getUserMedia({
          video: {
            facingMode: 'environment',
            width: { ideal: 4096 },
            height: { ideal: 2160 }
          },
          audio: false,
        });
        // maximize resolution based on hardware capabilities
        const [track] = this.stream.getVideoTracks();
        const capabilities = track.getCapabilities();
        const resolutionConstraints = {};
        if (capabilities.width) resolutionConstraints.width = { ideal: capabilities.width.max };
        if (capabilities.height) resolutionConstraints.height = { ideal: capabilities.height.max };
        await track.applyConstraints(resolutionConstraints);
        if (this.videoElement) {
          await nextTick();
          this.videoElement.srcObject = this.stream;
          this.videoElement.load();
          await this.videoElement.play();
          this.cameraHealthCheckTimer = setInterval(() => {
            if (this.showConfirm) {
              clearInterval(this.cameraHealthCheckTimer);
              this.cameraHealthCheckTimer = null;
              return;
            }

            const canvas = document.createElement('canvas');
            canvas.width = this.videoElement.videoWidth || 100;
            canvas.height = this.videoElement.videoHeight || 100;
            const ctx = canvas.getContext('2d');
            ctx.drawImage(this.videoElement, 0, 0, canvas.width, canvas.height);
            const frame = ctx.getImageData(0, 0, 1, 1).data;

            const hasImage = frame[0] + frame[1] + frame[2] > 0; // R+G+B > 0
            if (!hasImage) {
              clearInterval(this.cameraHealthCheckTimer);
              this.cameraHealthCheckTimer = null;
              //alert(this.$translate('instructions.reboot-device'));
            }
          }, 1000);
        }
      } catch (error) {
        console.error('Error accessing camera:', error);
      } finally {
        this.loading = false;
      }
    },
    async capturePhoto() {
      this.isCapturing = true;
      // ensure ImageCapture is created only when capturing
      if (!this.imageCapture && this.stream) {
        const [track] = this.stream.getVideoTracks();
        this.imageCapture = new ImageCapture(track);
      }
      if (!this.imageCapture) {
        console.warn('ImageCapture not ready');
        this.simpleCapture();
        return;
      }
      try {
        const blob = await this.imageCapture.takePhoto();
        const img = new Image();
        const url = URL.createObjectURL(blob);
        img.onload = () => {
          // Dynamic preview sizing based on video element
          const sensorW = img.naturalWidth;
          const sensorH = img.naturalHeight;
          const screenW = this.videoElement.clientWidth;
          const screenH = this.videoElement.clientHeight;

          const sensorAspect = sensorW / sensorH;
          const screenAspect = screenW / screenH;

          let sx = 0, sy = 0, sw = sensorW, sh = sensorH;
          if (sensorAspect > screenAspect) {
            const targetW = sensorH * screenAspect;
            sx = (sensorW - targetW) / 2;
            sw = targetW;
          } else if (sensorAspect < screenAspect) {
            const targetH = sensorW / screenAspect;
            sy = (sensorH - targetH) / 2;
            sh = targetH;
          }

          // Draw full-resolution cropped image matching preview framing
          this.fullCanvas.width = sw;
          this.fullCanvas.height = sh;
          const fullCtx = this.fullCanvas.getContext('2d', { willReadFrequently: true });
          fullCtx.imageSmoothingEnabled = false;
          fullCtx.drawImage(img, sx, sy, sw, sh, 0, 0, sw, sh);
          // populate file info panel
          this.fileInfo.width = sw;
          this.fileInfo.height = sh;
          this.fullCanvas.toBlob(blob => {
            this.fileInfo.size = blob.size;
          }, 'image/png');
          // stash full-resolution image for magnifier
          this.fullResData = this.fullCanvas.toDataURL('image/png');

          // Draw preview at device resolution
          const deviceW = this.videoElement.clientWidth;
          const deviceH = this.videoElement.clientHeight;
          this.previewCanvas.width = deviceW;
          this.previewCanvas.height = deviceH;
          const previewCtx = this.previewCanvas.getContext('2d');
          previewCtx.imageSmoothingEnabled = true;
          previewCtx.drawImage(
            img,
            sx, sy, sw, sh,
            0, 0, deviceW, deviceH
          );

          // Set the preview image
          this.currentImageData = this.previewCanvas.toDataURL('image/png');
          this.showConfirm = true;
          this.startAutoApproveTimer();
          this.isCapturing = false;
          URL.revokeObjectURL(url);
        };
        img.src = url;
      } catch (error) {
        console.error('ImageCapture error:', error);
        // Fallback: simple canvas from video
        this.simpleCapture();
      }
    },
    simpleCapture() {
      // original canvas fallback
      if (!this.videoElement) return;
      const vw = this.videoElement.videoWidth;
      const vh = this.videoElement.videoHeight;
      const cw = this.videoElement.clientWidth;
      const ch = this.videoElement.clientHeight;
      const displayAspect = cw / ch;
      const videoAspect = vw / vh;
      let sx, sy, sw, sh;
      if (videoAspect > displayAspect) {
        sw = vh * displayAspect;
        sh = vh;
        sx = (vw - sw) / 2;
        sy = 0;
      } else {
        sw = vw;
        sh = vw / displayAspect;
        sx = 0;
        sy = (vh - sh) / 2;
      }
      this.captureCanvas.width = sw;
      this.captureCanvas.height = sh;
      const ctx = this.captureCanvas.getContext('2d', { willReadFrequently: true });
      ctx.drawImage(this.videoElement, sx, sy, sw, sh, 0, 0, sw, sh);
      this.currentImageData = this.captureCanvas.toDataURL('image/png');
      // ensure magnifier still has full-res fallback
      this.fullResData = this.currentImageData;
      this.showConfirm = true;
      this.startAutoApproveTimer();
      this.isCapturing = false;
    },
    async retakePhoto() {
      this.fileInfo = { width: 0, height: 0, size: 0 };
      this.clearAutoApproveTimer();
      this.showConfirm = false;
      this.currentImageData = null;
      if (this.videoElement && this.stream && this.stream.active) {
        this.videoElement.srcObject = this.stream;
        await this.videoElement.play();
      }
      this.isCapturing = false;
    },

    // Refactored usePhoto: reset UI state before enqueuing upload
    async usePhoto() {
      this.clearAutoApproveTimer();

      // Reset UI immediately
      this.showConfirm = false;
      this.currentImageData = null;
      if (this.videoElement && this.stream && this.stream.active) {
        this.videoElement.srcObject = this.stream;
        await this.videoElement.play();
      }

      // Enqueue upload in background
      const fullCanvasCopy = document.createElement('canvas');
      fullCanvasCopy.width = this.fullCanvas.width;
      fullCanvasCopy.height = this.fullCanvas.height;
      fullCanvasCopy.getContext('2d').drawImage(this.fullCanvas, 0, 0);

      const id = Date.now() + '-' + Math.random().toString(36).slice(2);
      const dataURL = fullCanvasCopy.toDataURL('image/png');
      const job = {
        id,
        canvas: fullCanvasCopy,
        currency: stateTagApp.$read('camera.currency') ? stateTagApp.$read('currencies.selected') : null,
        endpoint: this.endpointUrl,
        batch: this.batchId || null,
      };
      // Persist the job for app restarts
      await this.saveQueuedJob({ id, dataURL, currency: job.currency, endpoint: job.endpoint, batch: job.batch });

      // Always add the job to the upload queue
      this.uploadQueue.push(job);
      this.uploadQueueTotal += 1;
      if (!this.uploadingInProgress) {
        this.processUploadQueue();
      }

      // If in batch mode, also track it inside the current batch for UI grouping
      if (this.isBatchMode && this.batches.length > 0) {
        this.batches[this.batches.length - 1].push(job);
      }

      if (this.autoApproveDurations[this.selectedAutoApproveIndex] > 0) {
        this.startAutoCaptureCountdown();
      }
    },

    // The actual upload logic, formerly usePhoto, now private and called per job
    async _enqueuePhotoUpload(canvas, currency, endpoint, batch = null) {
      try {
        const factor = (this.resolutionFactor || 100) / 100;
        const finalW = Math.floor(canvas.width * factor);
        const finalH = Math.floor(canvas.height * factor);
        const finalCanvas = document.createElement('canvas');
        finalCanvas.width = finalW;
        finalCanvas.height = finalH;
        const finalCtx = finalCanvas.getContext('2d');
        finalCtx.imageSmoothingEnabled = false;
        finalCtx.drawImage(canvas, 0, 0, canvas.width, canvas.height, 0, 0, finalW, finalH);
        const blob = await new Promise(resolve => {
          finalCanvas.toBlob(resolve, 'image/png');
          this.uploadProgress = 25;
        });
        const formData = new FormData();
        if (currency) {
          formData.append('currency', currency);
        }
        if (batch !== null) {
          formData.append('batch', batch);
        }
        formData.append('file', blob, 'photo.png');
        this.uploadProgress = 75;
        await this.$api(endpoint, 'Builder', formData); // let Axios set multipart boundary
        if (endpoint.includes('receipts')) {
          stateTagApp.$read('ai').activated = 1;
        } else {
          let url = endPoints.files.list.replace('{project}', this.$read('project.id'));
          const response = await this.$api(url, 'Builder');
          this.$write('builder.files', response.data);
        }
      } catch (error) {
        console.error('Upload failed:', error);
        alert('Upload failed. Check console for details.');
        this.$write('errors.builder', this.$translate('errors.saving'));
      } finally {
        this.uploadProgress = 100;
        setTimeout(() => { this.uploadProgress = 0; }, 400);
      }
    },

    // Helper to process a batch after all photos are uploaded
    async _processBatch(endpoint, batchId) {
      try {
        await this.$api(endpoint, 'Builder', { batch: batchId }, 'post');
      } catch (e) {
        console.error('Batch process failed:', e);
      }
    },

    async _enqueueProcessBatchJob() {
      if (!this.batchId) return;                       // nothing to do

      const jobId   = 'batch-' + this.batchId;
      const endpoint = endPoints.receipts.batch.replace(
          '{project}',
          this.$read('project.id')
      );

      const job = { id: jobId, type: 'processBatch', endpoint, batch: this.batchId };

      await this.saveQueuedJob(job);                   // persist for restart
      this.uploadQueue.push(job);                      // FIFO → runs after photos
      this.uploadQueueTotal += 1;
      if (!this.uploadingInProgress) this.processUploadQueue();

      this.batchId = null;                             // clear state
    },

    // Processes the upload queue one at a time in background
    async processUploadQueue() {
      if (this.uploadQueue.length === 0) {
        this.uploadingInProgress = false;
        this.uploadQueueTotal = 0;
        return;
      }
      this.uploadingInProgress = true;
      const { id, canvas, currency, endpoint, type, batch } = this.uploadQueue.shift();
      if (type === 'processBatch') {
        await this._processBatch(endpoint, batch);
      } else {
        await this._enqueuePhotoUpload(canvas, currency, endpoint, batch);
      }
      await this.deleteQueuedJob(id);
      // Process next if any
      this.processUploadQueue();
    },
    toggleOrientation() {
      this.isLandscape = !this.isLandscape;
    },
    async stopCamera() {
      if (this.cameraHealthCheckTimer) {
        clearTimeout(this.cameraHealthCheckTimer);
        this.cameraHealthCheckTimer = null;
      }
      if (this.stream) {
        this.stream.getTracks().forEach((track) => track.stop());
        this.stream = null;
      }
      if (this.videoElement) {
        this.videoElement.srcObject = null;
      }
    },
    startAutoApproveTimer() {
      this.clearAutoApproveTimer();
      const delay = this.autoApproveDurations[this.selectedAutoApproveIndex];
      if (delay > 0 && this.showConfirm) {
        this.autoApproveRemaining = delay;
        this.autoApproveCountdownTimer = setInterval(() => {
          this.autoApproveRemaining -= 250;
          if (this.autoApproveRemaining <= 0) {
            this.clearAutoApproveTimer();
            this.usePhoto();
          }
        }, 250);
      }
    },
    clearAutoApproveTimer() {
      if (this.autoApproveCountdownTimer) {
        clearInterval(this.autoApproveCountdownTimer);
        this.autoApproveCountdownTimer = null;
      }
      this.autoApproveRemaining = 0;
      if (this.autoApproveTimer) {
        clearTimeout(this.autoApproveTimer);
        this.autoApproveTimer = null;
      }
    },
    toggleAutoMode() {
      const active = this.autoCaptureCountdownTimer || this.autoApproveCountdownTimer;
      if (active) {
        this.clearAutoCaptureCountdown();
        this.clearAutoApproveTimer();
        this.selectedAutoApproveIndex = 0;
      } else {
        this.selectedAutoApproveIndex = (this.selectedAutoApproveIndex + 1) % this.autoApproveDurations.length;
      }
    },

    createNewBatchQueue() {
      this.batches.push([]);
      // cosmetic upload‑bar per batch (removed, handled by computed activeBatchIds)
    },

    processLastBatchQueue() {
      this.batches.pop();   // cosmetic only; bars handled automatically
    },

    async toggleBatchMode() {
      const wasBatch = this.isBatchMode;
      this.isBatchMode = !wasBatch;

      if (this.isBatchMode) {
        // starting a new batch
        this.batchId = stateTagApp.uuid();
        this.createNewBatchQueue();
      } else {
        // ending batch mode: only enqueue if batch has items
        const lastBatch = this.batches[this.batches.length - 1] || [];
        if (lastBatch.length > 0) {
          await this._enqueueProcessBatchJob();
        }
        this.processLastBatchQueue();
      }
    },

    async closeCamera() {
      if (this.isBatchMode) {
        // only enqueue if there are images in the batch
        const lastBatch = this.batches[this.batches.length - 1] || [];
        if (lastBatch.length > 0) {
          await this._enqueueProcessBatchJob();
        }
        this.processLastBatchQueue();
        this.isBatchMode = false;
      }
      this.$write('nav.camera', false);
      this.showConfirm = false;
      this.$write('camera.auto', 0);
    },
  },
};
</script>

