<template>
  <transition name="undo-slide">
    <button
        v-show="show"
        @click.prevent.stop="undo"
        class="undo-pill btn"
    >
      <span>{{ timer }}</span>
      <i class="bi bi-arrow-counterclockwise"></i>
    </button>
  </transition>
</template>

<script>

export default {

  data: function () {
    return {
      timer: 0,
      interval: null
    }
  },

  computed: {
    show() {
      return !!stateTagApp.$read("sta").undo.timeoutId;
    }
  },
  methods: {
    undo() {
      let timeoutId = stateTagApp.$read("sta.undo.timeoutId")
      stateTagApp.$write("sta.undo.timeoutId", null);

      clearInterval(this.interval);
      clearTimeout(timeoutId)

      // Trigger the promise rejection for the undo
      const undoReject = stateTagApp.$read("sta.undo.reject");
      if (undoReject) {
          undoReject(new Error("Undo clicked"));
      }
      stateTagApp.$write("sta.undo.reject", null);
    }
  },

  watch: {
    show(newVal) {
      if (newVal) {
        this.timer = stateTagApp.$read("sta.undo.delay");

        this.interval = setInterval(() => {
          this.timer--;
          if (this.timer < 1) {
            clearInterval(this.interval);
          }
        }, 1000);
      }
    }
  }
};
</script>

<style scoped>
.undo-pill {
  z-index: 99999999;
  background: rgba(220, 112, 112, 0.8);
  color: #fff;
  border: none;
  border-radius: 50px;
  padding: 0.5rem 1rem;
  position: absolute;
  margin-left: calc(50vw - 1em);
  margin-top: 20px;
  font-size: 2em;
  /*transform: translateX(-50%);*/
}

.undo-slide-enter-active,
.undo-slide-leave-active {
  transition: transform 0.3s, opacity 0.3s;
}

.undo-slide-enter {
  transform: translateY(-100%);
  opacity: 0;
}

.undo-slide-enter-to {
  transform: translateY(0);
  opacity: 1;
}

.undo-slide-leave {
  transform: translateY(0);
  opacity: 1;
}

.undo-slide-leave-to {
  transform: translateY(-100%);
  opacity: 0;
}

button span {
  position: relative;
  top: .8em;
  left: .7em;
  display: block;
  border: 1px solid red;
  width: 1px;
  height: 1px;
  overflow: visible;

  font-size: 0.5em;
}
</style>
