<template>
  <!-- Backdrop Fade Transition -->
  <div
      v-show="$read('nav.team')"
      class="modal"
      :class="{ 'd-block': $read('nav.team') }"
      style="background: rgba(0, 0, 0, 0.5); transition: opacity 0.5s ease;"
  >
    <!-- Slide Transition for Modal Content -->
    <transition name="modal-slide" appear>
      <div class="modal-dialog modal-slide" v-show="$read('nav.team')">
        <div class="modal-content">
          <!-- Modal Header -->
          <div class="modal-header">
            <h5 class="modal-title">{{ $translate('teams.modal-title') }}</h5>
            <button type="button" class=" btn btn-white" data-dismiss="modal" @click="$write('nav.team', false)">
              <i class="fas fa-times"></i>
            </button>
          </div>

          <div class="error">
            <x-text-html locus="errors.team"></x-text-html>
          </div>

          <!-- Modal Body -->
          <div class="modal-body">

            <div class="card">
              <div class="card-header bg-primary text-white text-center fw-bold">
                {{ $read('license.label') }}
              </div>
              <div class="card-body">
                <div>
                  <label for="teammates-progress" class="form-label"><strong>{{ $translate('teams.teammates') }}:</strong></label>
                  <div class="progress mb-2">
                    <div
                        class="progress-bar bg-success"
                        role="progressbar"
                        :style="{ width: teammatesBar + '%' }"
                        :aria-valuenow="teammatesBar"
                        aria-valuemin="0"
                        aria-valuemax="100"
                    >
                    </div>
                  </div>
                </div>
                <div>
                  <label for="receipts-progress" class="form-label"><strong>{{ $translate('teams.utilization') }}:</strong></label>
                  <div class="progress">
                    <div
                        class="progress-bar bg-danger"
                        role="progressbar"
                        :style="{ width: receiptsBar + '%' }"
                        :aria-valuenow="receiptsBar"
                        aria-valuemin="0"
                        aria-valuemax="100"
                    >
                    </div>
                  </div>
                </div>
              </div>
            </div>

            <div class="btn-group w-100 my-3" role="group">
              <button
                  type="button"
                  class="btn btn-outline-primary btn-sm"
                  :class="{ active: (inputMode == 'search')? true : false }"
                  @click="inputMode = 'search'">
                {{ $translate('search') }}
              </button>
              <button
                  type="button"
                  class="btn btn-outline-primary btn-sm"
                  :class="{ active: (inputMode != 'search')? true : false }"
                  @click="inputMode = 'create'">
                {{ $translate('create') }}
              </button>
            </div>


            <form v-show="inputMode == 'create'" @submit.prevent="createTeammate">
              <p class="alert alert-danger" v-show="upgradeRequired">
                <i class="fas fa-user"></i>&nbsp; {{ $translate('instructions.upgrade-required') }}
              </p>

              <div class="input-group mb-2">
                <span class="input-group-text">
                  <i class="fa fa-globe"></i>
                </span>

                <select
                    class="form-control"
                    v-model="teammateLanguage">
                  <option
                      v-for="(language, index) in languageOptions"
                      :key="index"
                      :value="index"
                  >{{ index.toUpperCase() }} - {{ language }}
                  </option>
                </select>
              </div>


              <div class="input-group mb-2">
      <span class="input-group-text" id="name-addon">
        <i class="fa fa-user"></i>
      </span>
                <input
                    v-model="teammateName"
                    type="text"
                    class="form-control"
                    id="name"
                    aria-label="Name"
                    aria-describedby="name-addon"
                    required
                />
              </div>

              <div class="input-group mb-2">
      <span class="input-group-text" id="email-addon">
        <i class="fa fa-envelope"></i>
      </span>
                <input
                    v-model="teammateEmail"
                    type="email"
                    class="form-control"
                    id="email"
                    aria-label="Email"
                    aria-describedby="email-addon"
                    required
                />
              </div>

              <div class="input-group mb-2">
      <span class="input-group-text" id="permissions-addon">
        <i class="fa fa-lock"></i>
      </span>
                <select
                    v-model="teammateRole"
                    name="role"
                    class="form-select"
                    aria-label="Select Permissions"
                    aria-describedby="permissions-addon"
                    required
                >
                  <option v-for="(label, key) in userPermissions" :key="key" :value="key">
                    {{ label }}
                  </option>
                </select>
              </div>

              <button :disabled="upgradeRequired" type="button" @click="createTeammate" class="btn btn-primary w-100">
                {{ $translate('create') }}
              </button>
            </form>


            <x-search
                v-show="inputMode == 'search'"
                placeholder="Search"
                url="endPoints.search.users"
                @resultsUpdated="handleSearchResults"
            ></x-search>
            <!-- Search Results -->
            <ul v-show="searchResults.length > 0 && inputMode ==='search'" class="list-group">
              <li
                  v-for="user in searchResults"
                  :key="user.id"
                  class="list-group-item d-flex justify-content-between"
              >
                <!-- Name and Email -->
                <div class="text-wrap">
                  <strong style="font-size: 1em;">{{ user.name }}</strong> <br/>
                  <small class="text-muted" style="font-size: 0.8em;">{{ user.email }}</small>
                </div>

                <!-- Dropdown for user permissions -->
                <div class="ms-auto d-flex align-items-start" style="position: relative;">
                  <div class="btn-group">
                    <button
                        type="button"
                        class="btn btn-xs dropdown-toggle"
                        :class="{
            'btn-white': getUserCurrentRole(user.id) == 'none',
            'btn-light': getUserCurrentRole(user.id) !== 'none',
          }"
                        data-bs-toggle="dropdown"
                        aria-expanded="false"
                    >
          <span v-if="getUserCurrentRole(user.id) == 'none'">
            {{ $translate('teams.permissions') }}
          </span>
                      <span v-else>
            {{ $translate('teams.roles.'.concat(getUserCurrentRole(user.id))) }}
          </span>
                    </button>
                    <ul class="dropdown-menu" style="min-width: 150px; max-width: 150px;">
                      <li v-for="(label, key) in userPermissions" :key="key">
                        <a
                            class="dropdown-item"
                            href="#"
                            :class="{ active: getUserCurrentRole(user.id) === key }"
                            @click.prevent="setPermission(
                user.id,
                getUserCurrentRole(user.id) === key ? 'none' : key
              )"
                        >
                          {{ label }}
                        </a>
                      </li>
                    </ul>
                  </div>
                </div>
              </li>
            </ul>


          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import _Services from "./_Services.vue";

// Make sure you have `endPoints` accessible, e.g., via imports or global config:
// import endPoints from '@/path/to/endPoints';

export default {
  mixins: [_Services],

  data() {
    return {
      inputMode: ['search', 'create'][0],
      searchResults: [],
      teammateLanguage: '',
      teammateName: '',
      teammateEmail: '',
      teammateRole: '',
    }
  },

  computed: {
    languageOptions: () => {
      let data = stateTagApp.$read('langpack.options')
      return Object
          .keys(data)
          .sort() // Sort the keys alphabetically
          .reduce((obj, key) => {
            obj[key] = data[key];
            return obj;
          }, {});
    },

    userPermissions() {
      return this.$read('langpack.uiux.teams.roles');
    },

    // seatsAvailable() {
    //   return !this.upgradeRequired;
    // },

    upgradeRequired() {
      return (this.$read('license.users') - this.$read('license.teammates') <= 0);
    },

    teammatesBar() {
      const percentage = (this.$read('license.teammates') / this.$read('license.users')) * 100;
      return isNaN(percentage) ? 0 : Math.min(percentage, 100);
    },

    receiptsBar() {
      const percentage = (this.$read('license.receipts') / this.$read('license.limit')) * 100;
      return isNaN(percentage) ? 0 : Math.min(percentage, 100);
    },
  },

  methods: {
    createTeammate() {
      // 1) Validate required fields
      if (!this.teammateName.trim()) {
        //this.$write('errors.team', 'Name is required.');
        return;
      }
      if (!this.teammateEmail.trim()) {
        //this.$write('errors.team', 'Email is required.');
        return;
      }

      // 2) Validate email format with a simple RegEx
      const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      if (!emailRegex.test(this.teammateEmail)) {
        this.$write('errors.team', this.$translate('errors.invalid-email'));
        return;
      }

      // 3) Send data to server
      this.$api(
          endPoints.permissions.teammate.replace('{project}', this.$read('project.id')),
          'CreateTeammate',
          {
            'language': this.teammateLanguage,
            full_name: this.teammateName,
            email: this.teammateEmail,
            role: this.teammateRole,
          },
          'post'
      )
          .then(response => {
            // Success! (e.g., add the new user to your team array)
            const newTeammate = response.data;

            // Optionally update your local store or state with the new user
            let team = this.$read('team') || [];
            team.push(newTeammate);
            this.$write('team', team);

            this.inputMode = 'search';
            this.$write('nav.team', false);
          })
          .catch(error => {
            // Handle error
            console.error('Error creating user:', error);
            this.$write('errors.team', error?.response?.data?.message || 'Error creating user.');
          });
    },

    toggleInputMode() {
      this.inputMode = (this.inputMode === 'search')
          ? 'create'
          : 'search';
    },

    // This fires whenever the <x-search> component returns results
    handleSearchResults(item) {
      this.searchResults = item.detail[0] || [];
    },

    // Determine the existing role for a user (if they are on the team)
    getUserCurrentRole(userId) {
      const existingTeamMember = this.$read('team').find(member => member.id === userId);
      return existingTeamMember ? existingTeamMember.role : 'none';
    },

    setPermission(userId, role) {
      //console.log(userId, role, this.$read('user').id);

      if (userId == this.$read('user').id) {
        this.$write('errors.team', this.$translate('errors.team-me'));
        return;
      }

      if (role === 'none') {
        this.revokeRole(userId);
      } else {
        this.grantRole(userId, role);
      }
    },

    grantRole(userId, role) {
      const url = endPoints.permissions.grant
          .replace('{project}', this.$read('project.id'))
          .replace('{member}', userId);

      this.$api(url, 'GrantPermission', {role}, 'patch')
          .then(response => {
            //console.log(`User granted ${role} role`, response);

            let team = this.$read('team') || [];
            let updatedTeammate = response.data;
            const index = team.findIndex(teammate => teammate.id === updatedTeammate.id);

            if (index !== -1) {
              // Teammate exists in the team array, replace the existing record
              team.splice(index, 1, updatedTeammate);
              //console.log(`Updated teammate with ID ${updatedTeammate.id} in the team.`);
            } else {
              // Teammate does not exist in the team array, append the new teammate
              team.push(updatedTeammate);
              //console.log(`Added new teammate with ID ${updatedTeammate.id} to the team.`);
            }

            // Update the local store with the modified team array
            this.$write('team', team);
          })
          .catch(error => {
            console.error(`Error granting role ${role}:`, error);
          });
    },

    revokeRole(userId) {
      const url = endPoints.permissions.revoke
          .replace('{project}', this.$read('project.id'))
          .replace('{member}', userId);

      this.$api(url, 'RevokePermission', undefined, 'DELETE')
          .then(response => {
            //console.log('User revoked from project', response);

            // Retrieve the current team array from the local store, or initialize it as an empty array
            let team = this.$read('team') || [];

            // Extract the updated teammate object from the response
            let updatedTeammate = response.data;

            // Find the index of the teammate with the same ID in the team array
            const index = team.findIndex(teammate => teammate.id === updatedTeammate.id);

            if (index !== -1) {
              // Update the existing member's role to null or remove them from the team
              // Option 1: Set role to null
              //team.splice(index, 1, {...team[index], role: null});
              //console.log(`Updated teammate with ID ${updatedTeammate.id} to have no role.`);

              // Option 2: Remove the teammate from the team array
              team.splice(index, 1);
              //console.log(`Removed teammate with ID ${updatedTeammate.id} from the team.`);
            } else {
              // Optionally, handle the case where the member isn't found in the team
              console.warn(`Teammate with ID ${updatedTeammate.id} not found in the team.`);
            }

            // Update the local store with the modified team array
            this.$write('team', team);

            // Optionally, update any reactive data properties or state management stores
            // this.team = team;
          })
          .catch(error => {
            console.error('Error revoking user:', error);
          });
    }

  },
};
</script>

<style scoped>
.dropdown-menu {
  min-width: 150px !important;
  max-width: 150px !important;

}

a.active::before {
  content: "✓"; /* White checkmark */
