<template>
  <div class="carousel image-inspectors">
    <div class="carousel-track" :style="trackStyle">
      <div
        v-for="(url, index) in imageUrls"
        :key="index"
        class="page"
      >
        <x-image-inspector
          :url="url"
          :zoomFactor="zoomFactor"
          :zoomWidth="zoomWidth"
          :zoomHeight="zoomHeight"
          :limitBy="limitBy"
          :maxWidth="maxWidth"
          :maxHeight="maxHeight"
        ></x-image-inspector>
      </div>
    </div>
    <div class="dots">
      <span
        v-for="(_, index) in imageUrls"
        :key="index"
        :class="['dot', { active: index === currentIndex }]"
        @click="goTo(index)"
      ></span>
    </div>
  </div>
</template>

<script>
import _Services from "@/x-html/_Services";

export default {

  mixins: [_Services],
  props: {
    locus: {
      type: String,
      required: true
    },
    zoomFactor: {
      type: Number,
      default: 0.25
    },
    zoomWidth: {
      type: String,
      default: '60vw'
    },
    zoomHeight: {
      type: String,
      default: '40vw'
    },
    limitBy: {
      type: String,
      default: 'width',
      validator: val => ['width', 'height'].includes(val)
    },
    maxWidth: {
      type: String,
      default: '100vw'
    },
    maxHeight: {
      type: String,
      default: '80vh'
    }
  },
  data() {
    return {
      currentIndex: 0
    };
  },
  computed: {
    imageUrls() {
      return this.$read(this.locus);
    },
    trackStyle() {
      return {
        marginLeft: `-${this.currentIndex * 200}%`
      };
    }
  },
  methods: {
    goTo(index) {
      this.currentIndex = index;
    }
  }
};
</script>

<style scoped>
.carousel {
  display: flex;
  overflow: hidden;
  width: 100%;
  position: relative;
}

.page {
  flex-shrink: 0;
  width: 100%;
  transition: margin-left 0.3s ease;
}

.carousel-track {
  display: flex;
  width: 100%;
  transition: margin-left 0.3s ease;
}
.image-inspectors {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.dots {
  display: flex;
  justify-content: center;
  margin-top: 8px;
}

.dot {
  width: 10px;
  height: 10px;
  margin: 0 4px;
  border-radius: 50%;
  background-color: #ccc;
  cursor: pointer;
}

.dot.active {
  background-color: #333;
}
</style>
