<template>
  <div class="input-group">
    <button @click="toggleMode" type="button" class="btn btn-light">
      <i v-if="mode === 'date'" class="bi bi-calendar-event"></i>
      <i v-if="mode === 'range'" class="bi bi-calendar-range"></i>
    </button>
    <v-date-picker
        :mode="mode"
        :locale="$read('langpack.selected')"
        :popover="{ placement: 'center-start' }"
        :input-props="{
        placeholder: $translate(translationKey),
        class: 'date-input form-control',
        style: inputWidth
      }"
        @input="handleDateSelect"
        :value="displayDate"
    ></v-date-picker>
  </div>
</template>

<script>
// https://v2.vcalendar.io/api/v1.0/datepicker.html


import VDatePicker from 'v-calendar/lib/components/date-picker.umd'
/*import { format } from 'date-fns'*/
import _Services from "@/x-html/_Services";

export default {
  mixins: [_Services],
  components: {
    VDatePicker
  },
  computed: {
    inputWidth() {
      return stateTagApp.$read('epoch.mode') === 'date'
          ? 'width: 125px;'
          : 'width: 205px;';
    },
    displayDate() {
      const start = stateTagApp.$read('epoch.start');
      const end = stateTagApp.$read('epoch.end');
      const mode = stateTagApp.$read('epoch.mode');

      const toLocalDate = (str) => {
        const [y, m, d] = str.split('-').map(Number);
        return new Date(y, m - 1, d);
      };

      if (mode === 'date' && start) {
        return toLocalDate(start);
      } else if (mode === 'range' && start && end) {
        return {
          start: toLocalDate(start),
          end: toLocalDate(end)
        };
      }
      return null;
    },
    translationKey() {
      const mode = stateTagApp.$read('epoch.mode');
      return {
        date: 'select-date',
        range: 'select-date-range'
      }[mode];
    },
    mode: {
      get() {
        return stateTagApp.$read('epoch.mode');
      },
      set(val) {
        stateTagApp.$write('epoch.mode', val);
      }
    }
  },
  methods: {
    handleDateSelect(date) {
      const toYMD = (d) => {
        const y = d.getFullYear();
        const m = String(d.getMonth() + 1).padStart(2, '0');
        const day = String(d.getDate()).padStart(2, '0');
        return `${y}-${m}-${day}`;
      };

      if (_.isUndefined(date.end)) {
        stateTagApp.$write('epoch.start', toYMD(date));
      } else {
        stateTagApp.$write('epoch.start', toYMD(date.start));
        stateTagApp.$write('epoch.end', toYMD(date.end));
      }
    },
    toggleMode() {
      stateTagApp.$write('epoch.mode', this.mode === 'range' ? 'date' : 'range');
    },
/*    formatDate(date) {
      if (!date) return '';
      const d = new Date(date);
      const year = d.getFullYear();
      const month = String(d.getMonth() + 1).padStart(2, '0');
      const day = String(d.getDate()).padStart(2, '0');
      return `${year}-${month}-${day}`;
    }*/
  }
}
</script>

<style epochd>
.vc-popover-content-wrapper {
  z-index: 99999 !important;
}
.input-group {
  white-space: nowrap !important;
  flex-wrap: nowrap !important;
}
</style>
