<template>
  <!-- Backdrop Fade Transition -->
  <div
      class="modal"
      :class="{ 'd-block': hasContent }"
      v-show="hasContent"
      style="background: rgba(0, 0, 0, 0.5); transition: opacity 0.5s ease;"
  >
    <!-- Slide Transition for Modal Content -->
    <transition name="modal-slide" appear>
      <div class="modal-dialog modal-slide" v-show="hasContent">
        <div class="modal-content">

          <div class="modal-header">
            <h5 class="modal-title">{{ $read("modal.content.header") | translation }}</h5>
            <button v-show="$read('modal.options.x')" type="button" class=" btn btn-white" data-dismiss="modal" @click="clearModal">
              <i class="fas fa-times"></i>
            </button>
          </div>

          <x-image-inspector v-if="$read('modal.content.image')" locus="modal.content.image" zoom-width="80vw"></x-image-inspector>

          <x-image-inspectors v-if="$read('modal.content.images')" locus="modal.content.images" zoom-width="80vw"></x-image-inspectors>

          <div v-show="!_.isEmpty($read('errors.modal'))" class="error">
            <pre>{{ $read('errors.modal') | translation }}</pre>
          </div>

          <div class="modal-body"
               v-if="!isHtml($read('modal.content.body'))"
          >
            <p>{{ $read("modal.content.body") }}</p>
          </div>

          <div class="modal-body"
               v-else
               v-html="$read('modal.content.body')"></div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import _Services from "./_Services.vue";

export default {
  mixins: [_Services],
  computed: {
    hasContent() {
      return (
          this.$read("modal.content.header") ||
          this.$read("modal.content.body") ||
          this.$read("modal.content.footer") ||
          this.$read("modal.content.image")
      );
    },
  },
  methods: {
    clearModal() {
     /* this.$write("modal.content.header", "");
      this.$write("modal.content.body", "");
      this.$write("modal.content.footer", "");
      this.$write("modal.content.image", "");
      this.$write("modal.content.images", "");*/
      stateTagApp.commands.clear('modal');
    },

    isHtml(input) {
      return /<\/?[a-z][\s\S]*>/i.test(input.trim());
    }
  }
};
</script>

