<template>

  <div class="input-group">
    <span v-if="!_.isEmpty(icon)" class="input-group-text">
      <i :class="classes"></i>
    </span>
    <span v-else class="input-group-text">{{ $translate(name) }}</span>

    <select
        :name="name"
        class="form-control"
        v-model="serviceContent">
      <option
          v-for="(language, index) in options"
          :key="index"
          :value="index"
      >{{ index.toUpperCase() }} - {{ language }}
      </option>
    </select>
  </div>

</template>
<script>
import _Services from "./_Services"

export default {
  mixins: [_Services],

  props: {
    name: {
      type: String,
      required: true
    },
    icon: {
      type: String,
      required: false
    }
  },

  computed: {
    classes() {
      return 'fa fa-'.concat(this.icon)
    },

    options: () => {
      let data = stateTagApp.$read('langpack.options')
      return Object
          .keys(data)
          .sort() // Sort the keys alphabetically
          .reduce((obj, key) => {
            obj[key] = data[key];
            return obj;
          }, {});
    },
  }
}
</script>
<style scoped>
.input-group{
  margin-bottom: 10px;
}
</style>
