<template>

  <div class="input-group">
    <span v-if="!_.isEmpty(icon)" class="input-group-text">
      <i :class="classes"></i>
    </span>
    <span v-else class="input-group-text">{{ $translate(name) }}</span>

    <select
        v-if="!_.isEmpty(options)"
        :name="name"
        class="form-control"
        v-model="serviceContent">
      <option
          v-for="currency in options"
          :value="currency"
      >
        {{ currency }}
      </option>
    </select>
  </div>

</template>

<script>
import _Services from "./_Services"

export default {
  mixins: [_Services],

  props: {
    name: {
      type: String,
      required: true
    },
    icon: {
      type: String,
      required: false
    }
  },

  computed: {
    classes() {
      return 'fa fa-'.concat(this.icon)
    },

    options: () => {
     return stateTagApp.$read('currencies.options');
    },
  },

  methods: {
    setupMenu: function () {
      return this.$api(endPoints.currencies, 'setupCurrencyMenu')
          .then(result => {
            this.$write('currencies.options', result.data);
          });
    },
  },

  mounted: function(){
    this.setupMenu().then(() => {
      if(this.locus != 'currencies.selected'){
        if(_.isEmpty(this.$read(this.locus))){
          this.$write(this.locus, this.$read('currencies.selected'));
        }
      }
    }) ;
  }
}
</script>

<style scoped>
.input-group{
  margin-bottom: 10px;
}
</style>
