// Determine environment from subdomain first, fallback to NODE_ENV
var subdomain = window.location.hostname.split('.')[0];
var currentEnv = subdomain || process.env.NODE_ENV;

stateTagApp.log(stateTagApp.namespace.concat("-html: #BUILD#"));

import _ from "lodash";
import Vue from "vue";
import vueCustomElement from "vue-custom-element";
import Vue2TouchEvents from 'vue2-touch-events'
Vue.use(Vue2TouchEvents)

stateTagApp.Vue = Vue;

Vue.use(vueCustomElement);
Vue.use(require("vue-moment"));

Vue.config.productionTip = {
    production: true,
    development: false
}[process.env.NODE_ENV];

Vue.prototype._ = _;
import {v4 as uuidv4} from 'uuid';

stateTagApp['uuid'] = uuidv4;


import Api from "./api";

Vue.use(Api);


import Nebula from "./nebula";

Vue.use(Nebula);

import VueSocketIOExt from 'vue-socket.io-extended';
import io from 'socket.io-client';

if (!_.isUndefined(stateTagApp.socket)) {
    stateTagApp.socket = stateTagApp.socket[currentEnv];
    stateTagApp['socketIo'] = io(stateTagApp.socket);

    stateTagApp['$onSocket'] = function (event, handler) {
        stateTagApp['socketIo'].on(event, handler);
    }

    Vue.use(VueSocketIOExt, stateTagApp['socketIo'])
}
stateTagApp["$translate"] = function (key) {
    let locus = 'langpack.uiux.'.concat(key);
    let val = stateTagApp.$read(locus);

    return _.isEmpty(val) ? key : val;

}
Vue.filter("translation", function (key) {
    return stateTagApp.$translate(key);
});

Vue.filter("userLang", function(lang_stack){
    return lang_stack[stateTagApp.$read('langpack.selected')] || 'error';
});

Vue.filter("numberFormat", function (value, showFractionals = true) {
    const pattern = stateTagApp.$read('langpack.numberFormat');

    if (typeof value !== 'number') {
        value = parseFloat(value);
        if (isNaN(value)) return value;
    }

    const formattingRules = {
        '#,###.##':        { thousand: ',', decimal: '.' },
        '#.###,##':        { thousand: '.', decimal: ',' },
        '# ###,##':        { thousand: ' ', decimal: ',' },
        "#'###.##":        { thousand: "'", decimal: '.' },
        '##,##,###.##':    { thousand: ',', decimal: '.', indian: true },
        '######.##':       { thousand: '', decimal: '.' },
    };

    const rules = formattingRules[pattern] || { thousand: ',', decimal: '.' };

    const parts = value.toFixed(2).split('.');
    let intPart = parts[0];
    const decPart = parts[1];

    if (rules.indian) {
        let lastThree = intPart.slice(-3);
        let rest = intPart.slice(0, -3);
        if (rest) {
            rest = rest.replace(/\B(?=(\d{2})+(?!\d))/g, rules.thousand);
            intPart = rest + rules.thousand + lastThree;
        }
    } else if (rules.thousand) {
        intPart = intPart.replace(/\B(?=(\d{3})+(?!\d))/g, rules.thousand);
    }

    return showFractionals ? intPart + rules.decimal + decPart : intPart;
});

Vue.filter("date", function (value, format) {
    format = format || stateTagApp.format.date;
    let date = new Date(value);
    return stateTagApp.Vue.moment(date).format(format)
});

Vue.filter("k", function (value, format) {
    if (typeof value !== 'number') {
        // If the value isn't a number, return it as-is
        return value;
    }

    const absValue = Math.abs(value); // Handle negative numbers
    let formattedNumber;
    let suffix = '';

    if (absValue >= 1000000) {
        // For numbers >= 1,000,000
        formattedNumber = (value / 1000000).toFixed(1);
        suffix = 'm'; // You can use 'M' if preferred
    } else if (absValue >= 1000) {
        // For numbers >= 1,000 and < 1,000,000
        formattedNumber = (value / 1000).toFixed(1);
        suffix = 'k'; // You can use 'K' if preferred
    } else {
        // For numbers < 1,000
        return value.toString();
    }

    // Remove trailing '.0' if present (e.g., 2.0k → 2k)
    if (formattedNumber.endsWith('.0')) {
        formattedNumber = formattedNumber.slice(0, -2);
    }

    return formattedNumber + suffix;
});



Vue.filter('dynamicFilters', function (value, filters) {

    for (let i in filters) {
        let name = filters[i].name;
        let args = filters[i].args || [];
        value = Vue.filter(name)(value, ...args);
    }

    return value;
});

import state from "./state";
import router from "./routes"

initGlobalStateWatchers(stateTagApp["storage"]);
initGlobalSocketWatchers();
initGlobalNebulaWatchers(uuidv4, _);

import XHtml from "./x-html";

Vue.use(XHtml);
import Manifest from "@/x-html/manifest";

for (let tag in Manifest) {
    Manifest[tag].store = state;
    Manifest[tag].router = router;
    Vue.customElement(tag, Manifest[tag]);
}

setTimeout(() => {
    switch (true) { // START UI
        case (!_.isEmpty(stateTagApp.$read('passwordReset.token'))):
            stateTagApp.commands.showPasswordReset();
            break;
        case (_.isEmpty(stateTagApp.$read('user.token'))):
            stateTagApp.commands.showLogin();
            break;
    }

}, 250);


