<template>
  <div style="padding: 2em;">
    <!-- 1. Receipt loaded & portrait: rotate to landscape -->
    <div v-if="receipt && Object.keys(receipt).length && isPortrait" class="receipt-inspector-overlay portrait-mode">
      <i class="fa fa-mobile-alt fa-3x" aria-hidden="true"></i>
      <i class="fa fa-arrow-right fa-3x" aria-hidden="true"></i>
      <i class="fa fa-mobile-alt fa-3x fa-rotate-90" aria-hidden="true"></i>
    </div>
    <!-- 2. Receipt empty & landscape: rotate to portrait -->
    <!--    <div v-else-if="(!receipt || !Object.keys(receipt).length) && !isPortrait" class="receipt-inspector-overlay landscape-mode">
          <i class="fa fa-mobile-alt fa-3x fa-rotate-90" aria-hidden="true"></i>
          <i class="fa fa-arrow-right fa-3x mx-3" aria-hidden="true"></i>
          <i class="fa fa-mobile-alt fa-3x" aria-hidden="true"></i>
        </div>-->
    <!-- 3. Receipt loaded & landscape: show inspector -->
    <div v-else-if="receipt && Object.keys(receipt).length && !isPortrait" class="receipt-inspector-overlay landscape-mode">

      <button type="button"
              class="btn-close position-absolute"
              style="top: 1em; right: 1em; z-index: 10000;"
              aria-label="Close"
              onclick="stateTagApp.commands.clear('receipt')">
      </button>

      <div style="width: 55vw; height: 100vh; position: fixed; display: flex; justify-content: center;">
        <x-image-inspector zoom-width="100vh" zoom-height="35vh" limit-by="height" locus="receipt.photo" max-height="100vh"></x-image-inspector>
      </div>

      <div style="width: 45vw; margin-left: 55vw; padding-top: 1em; padding-bottom: 3em; padding-right: 3.5em;">
        <x-input name="storeName" type="text" locus="receipt.storeName" icon="fa fa-store"></x-input>
        <x-input-date name="date" locus="receipt.date" icon="fa fa-calendar"></x-input-date>
        <x-currency-input locus="receipt.currency" name="currency" icon="money-bill"></x-currency-input>

        <div v-for="(item, index) in $read('receipt.items')" :key="item.id" class="mb-3 p-2 border rounded bg-light">
          <div class="fw-bold pb-1 mb-2 border-bottom text-end" style="border-color: #dee2e6 !important;">
            {{ $read('receipt.symbol') }}{{ $read('receipt.items')['' + index].total | numberFormat }}
          </div>
          <div class="row g-1 align-items-center mb-2">
            <div class="col-2">
              <input
                  type="number"
                  class="form-control form-control-sm"
                  min="0"
                  :value="$read('receipt.items')['' + index].units"
                  @input="
                    $write('receipt.items.' + index + '.units', parseFloat($event.target.value));
                    $write('receipt.items.' + index + '.total',
                           parseFloat($event.target.value || 0) * parseFloat($read('receipt.items')['' + index].price || 0));
                  "
              />
            </div>
            <div class="col-1 text-center">
              <span>@</span>
            </div>
            <div class="col-9">
              <input
                  type="number"
                  class="form-control form-control-sm"
                  min="0"
                  step="0.01"
                  :value="$read('receipt.items')['' + index].price"
                  @input="
                    $write('receipt.items.' + index + '.price', parseFloat($event.target.value));
                    $write('receipt.items.' + index + '.total',
                           parseFloat($read('receipt.items')['' + index].units || 0) * parseFloat($event.target.value || 0));
                  "
              />
            </div>
          </div>
          <div class="mb-1 text-muted small pb-1 mb-2" style="border-color: #dee2e6 !important;">
            <strong>{{ item.description | userLang }}</strong>: {{ item.usage | userLang }}
<!--            <button class="btn btn-outline-secondary btn-sm" style="border: none; float:right;">
              <i class="fa fa-edit" style="font-size: 1.2em; position: relative; top: +.75em; right: -.75em;"></i>
            </button>-->
          </div>
<!--          <textarea
              class="form-control form-control-sm"
              :value="$read('receipt.items')['' + index].usage[$read('user.lang')]"
          />-->
        </div>

        <div v-show="!_.isEmpty($read('receipt.taxesAndFees'))" class="mb-4 p-3 border rounded bg-white">
          <div class="fw-bold border-bottom pb-2 mb-3 text-muted small">Taxes and Fees</div>
          <div
            v-for="(val, key) in $read('receipt.taxesAndFees')"
            :key="key"
            class="d-flex align-items-center mb-2"
          >
            <div class="flex-grow-1 text-capitalize">{{ val.description }}</div>
            <div class="w-50">
              <input
                type="number"
                step="0.01"
                class="form-control form-control-sm"
                :value="val.amount"
                @input="$set($read('receipt.taxesAndFees'), key, { ...val, amount: parseFloat($event.target.value) })"
              />
            </div>
          </div>
        </div>

        <div class="btn-group w-100 mt-4" role="group">
          <button type="button" class="btn btn-secondary" onclick="stateTagApp.commands.clear('receipt')">{{ $translate('cancel') }}</button>
          <button type="button" class="btn btn-primary" @click.prevent.stop="submitReceipt">{{ $translate('Save') }}</button>
        </div>

      </div>

    </div>
  </div>
</template>

<script>
import _Services from "@/x-html/_Services";
import _RotationMixin from '@/x-html/_RotationMixin'

export default {
  mixins: [_Services, _RotationMixin],
  computed: {
    receipt() {
      return this.$read("receipt");
    }
  },
  methods: {
    submitReceipt() {
      let receipt = this.$read("receipt");
      let receipts = stateTagApp.$read('receipts');

      // please find the array index of the receipt that matches the current receipt by receipt.id
      let index = receipts.findIndex(r => r.id == stateTagApp.$read("receipt").id);

      receipts[index] = {...receipt};

      let url = endPoints.receipts.overwrite
          .replace('{project}', stateTagApp.$read('project.id'))
          .replace('{receipt}', receipt.id);

      this.$api(url, 'OverwriteReceipt', receipt)
          .then(response => {
            let url = endPoints.receipts.list
                .replace('{project}', stateTagApp.$read('project.id'));

            this.$api(url, 'Receipts')
                .then((response) => {
                  this.$write("receipts", response.data);
                  stateTagApp.commands.clear('receipt');
                })
                .catch((error) => {
                  console.log(error);
                });
          })
    }
  }
};
</script>

