.portrait-mode {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.landscape-mode {
  width: 100%;
  height: 100vh;

  overflow: scroll;
}

i {
  color: #999999;
  font-size: 2rem;
  margin-right: 0.5em;
}

</style>
