<template>
  <form class="needs-validation" novalidate @submit.prevent="validateAndSubmitForm($event, submitForm)">

    <x-input
        locus="builder.name"
        type="text"
        name="name"
        icon="folder"
        required
    ></x-input>

    <x-lang-input
        locus="builder.language"
        name="language"
        icon="language"
    >
    </x-lang-input>

    <x-currency-input
        locus="builder.currency"
        name="currency"
        icon="money-bill"
    ></x-currency-input>

    <hr>
    <div class="alert alert-info small">
      {{ $translate('instructions.ongoing-budget') }}
    </div>
    <x-input-number
        locus="builder.budget"
        name="budget"
        icon="chart-pie"
        min=0
    ></x-input-number>


    <div class="btn-group w-100" role="group">
      <button type="submit" class="btn btn-primary">
        {{ $translate('save') }}
      </button>
    </div>

  </form>
</template>

<script>
import _Services from "@/x-html/_Services";
import _FormValidation from "@/x-html/_FormValidation";

export default {
  mixins: [_Services, _FormValidation],
  methods: {
    cancel () {
      stateTagApp.commands.clear('builder');
      stateTagApp.commands.hideModal();
    },
    submitForm: function (formData) {
      formData = {...formData, status: 'rendering' };
      this.$api(endPoints.projects.store, 'Project', formData)
          .then(response => {
            stateTagApp.commands.hideModal();
            this.$broadcast({from: 'Project', type: 'api', event: 'project-created', ...response.data});
            let projects = this.$read('projects');
            projects.push(response.data.data);
            this.$write('projects', projects);
            this.$write('project', response.data.data);
            stateTagApp.commands.clear('builder');
            this.$write('nav.builder', true);
          })
          .catch(error => {
            console.log(error);
            this.$write('errors.modal', '500');
          });
    }
  }
};
</script>
