<script>
//import _Services from "@/x-html/_Services";

export default {
  //mixins: [_Services],
  methods: {
    validateAndSubmitForm: function (e, submitForm) {
      const form = e.target;
      const inputs = Array.from(form.querySelectorAll('.form-control'));
      let allValid = true;

      inputs.forEach(input => {
        if (!input.checkValidity()) {
          input.reportValidity();
          allValid = false;
        }
      });

      if (allValid) {
        const formData = inputs.reduce((acc, input) => {
          acc[input.name] = input.value.trim();
          return acc;
        }, {});

        submitForm(formData);
      }
    }
  }
};
</script>
