<template>
  <div class="dropup align-left">
    <button
        type="button"
        class="btn btn-light"
        id="teamsDropup"
        data-bs-toggle="dropdown"
        aria-expanded="false"
    >
      <i class="fas fa-search fa-lg"></i>
    </button>

    <div class="dropdown-menu p-4 manage-dropdown" aria-labelledby="teamsDropup">
      <h5 class="dropdown-header">{{ $translate('searches.project-searches') }}</h5>

      <ul v-if="$read('searches').length > 0" class="list-group mb-3 list-area">
        <transition-group
            name="searchfade"
            tag="div"
        >
          <li
              v-for="(search, index) in $read('searches')"
              :key="search.id || index"
              class="list-group-item d-flex justify-content-between align-items-center"
              @click.prevent="loadSearchResults(search)"
          >
            <div class="d-flex align-items-center">
              <span class="badge bg-info me-2">{{ search.matches }}</span>
              <div>
                <small class="text-muted">{{ search.title | userLang }}</small>
              </div>
            </div>


            <button v-show="activeSearch == search.id" @click.prevent.stop="() => {}" type="button" class="btn btn-light btn-sm delete-button" disabled>
              <i class="fas fa-check"></i>
            </button>

            <button
                v-show="activeSearch != search.id"
                type="button"
                class="btn btn-outline-danger"
                @click.stop.prevent="deleteSearch(search)"
            >
              <i class="fas fa-trash-alt"></i>
            </button>

          </li>
        </transition-group>
      </ul>

      <p v-else class="text-muted">{{ $translate('searches.no-hisotry') }}</p>

      <hr/>

      <div class="d-flex justify-content-between">
        <button
            @click.prevent="activateMic"
            type="button"
            class="btn btn-primary d-flex align-items-center"
        >
          <i class="fas fa-plus me-2"></i>
          <span>{{ $translate('new') }}</span>
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import _Services from "@/x-html/_Services";

export default {
  mixins: [_Services],
  data() {
    return {
      activeSearch: null,
      loadingSearchHistory: false,
    };
  },

  computed: {
    projectId() {
      return this.$read("project").id;
    },
    notification() {
      return stateTagApp.$read("notification");
    }
  },

  methods: {
    redo(search) {
      const url = endPoints.search.redo
          .replace('{project}', this.projectId)
          .replace('{search}', search.id)

      this.$api(url, "RedoSearch", {_method: 'PUT'}, "PUT")
          .then(() => {
          })
          .catch(error => {
            // Restore the original list in case of error
            console.log(error);
          });

    },
    deleteSearch(search) {
      // Keep the original list in case we need to restore on API failure
      const originalSearches = [...this.$read("searches")];

      // Remove locally for a smooth transition
      const updatedSearches = originalSearches.filter(s => s.id !== search.id);
      this.$write("searches", updatedSearches);

      // Make API call
      const url = endPoints.search.delete
          .replace("{project}", this.projectId)
          .replace("{search}", search.id);

      this.$api(url, "DeleteSearch".concat(search.id), undefined, "DELETE")
          .then(() => {
            // Optionally re-sync if needed
          })
          .catch(error => {
            console.error(error);
            // Restore original list if deletion failed
            this.$write("searches", originalSearches);
          });
    },

    loadSearchResults(search) {
      // Toggle search results
      if (this.activeSearch == search.id) {
        this.activeSearch = null;
        // Load full receipts list
        let url = endPoints.receipts.list.replace("{project}", this.projectId);
        url = this.epochGetString(url);
        this.$api(url, "Receipts")
            .then(response => {
              this.$write("receipts", response.data);
            })
            .catch(error => {
              console.error(error);
            });
      } else {
        this.activeSearch = search.id;
        const url = endPoints.search.results
            .replace("{project}", this.projectId)
            .replace("{search}", search.id);
        this.$api(url, "Receipts")
            .then(response => {
              this.$write("receipts", response.data);
            })
            .catch(error => {
              console.error(error);
            });
      }
    },

    activateMic() {
      this.$write("microphone.instruction", this.$translate("instructions.describe-receipt"));
      this.$write("microphone.endpoint", endPoints.search.receipts);
      this.$write("nav.microphone", true);
    },

    async loadSearchHistory() {
      if (!this.projectId) return;

      this.loadingSearchHistory = true;
      const url = endPoints.search.history.replace("{project}", this.projectId);
      return this.$api(url, "SearchNav")
          .then(response => {
            stateTagApp.$write("searches", response.data);
          })
          .finally(() => {
            this.loadingSearchHistory = false;
          });
    }
  },

  watch: {
    projectId: {
      deep: false,
      immediate: false,
      handler(fresh, stale) {
        this.loadSearchHistory();
      }
    },

    notification: {
      deep: false,
      immediate: false,
      handler(fresh, stale) {
        this.$nextTick(() => {
          console.log('ran SearchNav notification watcher');

          if (!fresh.id || fresh.model != 'search') {
            return;
          }

          const tryLoad = () => {
            if (fresh.project != this.projectId
                || this.loadingSearchHistory) {
              setTimeout(tryLoad, 250);
              return;
            }

            this.loadSearchResults(fresh);
            stateTagApp.commands.clear('notification');
          };

          tryLoad();

        });
      }
    }
  },

  mounted() {
    this.loadSearchHistory();
  }
};
</script>

<style scoped>
.list-area {
  max-height: 30vh;
  overflow-y: scroll;
}

/* Transition for smooth removal */
.searchfade-enter-active, .searchfade-leave-active {
  transition: all 0.3s ease;
}

.searchfade-enter, .searchfade-leave-to {
  opacity: 0;
  transform: translateY(10px);
}


</style>
