<template>
  <select
      id="languageSelect"
      class="form-control"
      v-model='selected'
  >
    <option
        v-for="(language, index) in options"
        :key="index"
        :value="index"
    >{{ index.toUpperCase() }} - {{ language }}
    </option>
  </select>
</template>
<script>
//import _Services from "./_Services.vue";

export default {
  //mixins: [_Services],

  computed: {
    options: (val) => {
      let data = stateTagApp.$read('langpack.options')
      return Object
          .keys(data)
          .sort() // Sort the keys alphabetically
          .reduce((obj, key) => {
            obj[key] = data[key];
            return obj;
          }, {});
    },
    selected: {
      get: () => {
        return stateTagApp.$read('langpack.selected');
      },
      set: (val) => {
        stateTagApp.$write('langpack.selected', val);
      }
    }
  },

  watch: {
    selected: function(val, original) {
      this.loadlanpackForUIUX();
    }
  },

  methods: {
    setupMenu: function () {
      this.$api(endPoints.languages, 'setupLangMenu')
          .then((result) => {
            this.$write('langpack.options', result.data);
            this.loadlanpackForUIUX();
          });
    },

    loadlanpackForUIUX: function () {

      let url = endPoints.languagePack
          .replace('{pack}', 'uiux')
          .replace('{lang}', this.$read('langpack.selected'));

      this.$api(url, 'loadlanpackForUIUX')
          .then((result) => {
            this.$write('langpack.uiux', result.data)
          });
    }
  },

  mounted: function () {
    this.setupMenu();
  }
}
</script>
