<template>
  <div v-show="!_.isEmpty(user.email)" class="dropup">
    <button
        type="button"
        class="btn btn-light"
        id="userDropup"
        data-bs-toggle="dropdown"
        aria-expanded="false"
    >
      <i class="fas fa-user fa-lg"></i>
    </button>

    <div class="dropdown-menu p-4" aria-labelledby="userDropup">
      <h5 class="dropdown-header">
        <x-text-html locus="user.first_name"></x-text-html>
        <x-text-html locus="user.last_name"></x-text-html>
      </h5>

      <p v-show="$read('errors.user')" class="alert alert-danger">{{ $read('errors.user') }}</p>

      <!-- Subscription Info Layout -->
      <div v-show="customer.subscription.name && !changePlanVisible" class="card shadow-sm my-3">
        <div class="card-body">
          <h5 class="card-title">
            <i class="bi bi-plan"></i>
            {{ customer.subscription.name }}
          </h5>
          <!-- Dynamic badge based on subscription.status -->
          <span :class="['badge', subscriptionStatusClass]">
            {{ $translate('stripe.'.concat(customer.subscription.status)) }}
          </span>

          <hr>
          <p v-if="customer.card.last4" class="mb-2">
            <div style="padding-left: 1em; float: right; font-size: 0.7em; font-weight: lighter;">
              {{ customer.card.expires }}<br/>{{ $translate('stripe.expires') }}
            </div>
            <i :class="cardBrandIcon" style="font-size: 1.7em;"></i>
            **** **** **** <span style="font-size: 1.7em;">{{ customer.card.last4 }}</span>
          </p>
          <div v-if="customer.subscription.paidOn.status !== 'paid'" class="border rounded p-3 bg-light">
            <span style="position: relative; top: -0.7em;"
                  :class="{
                  'badge': true,
                  'badge-active': customer.subscription.paidOn.status === 'paid',
                  'badge-incomplete': customer.subscription.paidOn.status !== 'paid'
                }"
            >
                {{ $translate('stripe.'.concat(customer.subscription.paidOn.status)) }}
              </span>
            <h6 class="text-muted mb-1">
              {{ $translate('stripe.last_payment') }}
            </h6>
            <hr/>
            <p class="mb-1">{{ $translate('date') }}: {{ customer.subscription.paidOn.date }}</p>
            <p class="mb-1">{{ $translate('amount') }}: ${{ customer.subscription.paidOn.amount }}</p>
          </div>
          <p v-if="customer.subscription.until" class="mt-3">
            <strong>{{ $translate('stripe.until') }}:</strong> {{ customer.subscription.until }}
          </p>
        </div>
        <div class="card-footer d-flex" style="width: 100%;">

          <form v-show="showCardForm" class="position-relative">
            <div id="card-errors" role="alert" class="text-danger mb-2"></div>
            <div class="input-group">
              <!-- Stripe's Card Element -->
              <div id="card-element" class="form-control"></div>
              <!-- Save Button -->
              <button type="button" @click.prevent.stop="updateCard" class="btn btn-primary btn-sm">
                <i class="fa fa-check"></i>
              </button>

              <button type="button" @click.prevent.stop="toggleCardForm" class="btn btn-secondary btn-sm">
                <i class="fa fa-times"></i>
              </button>
            </div>
          </form>

          <div v-show="!showCardForm" class="btn-group ms-auto" role="group" aria-label="Stripe Actions">

            <button @click.stop.prevent="toggleCardForm" class="btn btn-light btn-xs ms-auto">
              {{ $translate('stripe.update_card') }}
            </button>

            <button v-if="customer.card.last4 !== '0000'" @click.stop.prevent="mightUpgrade" class="btn btn-light btn-xs ms-auto">
              {{ $translate('stripe.upgrade') }}
            </button>

            <span v-show="customer.subscription.status === 'active' && customer.subscription.name !== 'Free License'" class="ms-auto">
              <input type="checkbox" v-model="doubleCancel" class="double-cancel"/>
              <button style="padding-left: 2.25em;" :disabled="!doubleCancel" @click.stop.prevent="cancelMembership" class="btn btn-danger btn-xs">
                {{ $translate('stripe.cancel') }}
              </button>
            </span>
          </div>

        </div>

      </div>
      <!-- End Subscription Info -->

      <div v-show="changePlanVisible" class="card shadow-sm my-3">
        <div class="card-body">
          <h5 class="card-title">
            <i class="bi bi-plan"></i>
            {{ $translate('stripe.membership-required') }}
          </h5>
          <p class="card-text text-muted">{{ $translate('instructions.membership-required') }}</p>

          <form style="min-width: 100%;" class="position-relative d-flex flex-column flex-grow-1">
            <div class="input-group">
              <select class="form-control" v-model="plan">
                <option v-for="planItem in paidPlans" :key="planItem.stripe_id" :value="planItem.stripe_id">
                  {{ planItem.price }} {{ planItem.label }}
                </option>
              </select>
              <button type="button" @click.prevent.stop="changePlan" class="btn btn-primary btn-sm">
                <i class="fa fa-check"></i>
              </button>
              <button type="button" @click.prevent.stop="cancelChangePlan" class="btn btn-secondary btn-sm">
                <i class="fa fa-times"></i>
              </button>
            </div>
          </form>
        </div>
      </div>

      <h4>
        <i class="fa fa-globe"></i>
        {{ $translate('data-display') }}</h4>
      <x-lang @change="langChanged"></x-lang>
      <x-number-format></x-number-format>

      <div class="btn-group btn-group-toggle w-100 mb-3" data-bs-toggle="buttons">
        <label class="btn btn-outline-secondary btn-sm"
               :class="{ active: numberCurrency === 'receipt' }"
               @click.prevent="numberCurrency = 'receipt'">
          {{ $translate('receipt.self') }} {{ $translate('receipt.currency') }}
        </label>
        <label class="btn btn-outline-secondary btn-sm"
               :class="{ active: numberCurrency === 'project' }"
               @click.prevent="numberCurrency = 'project'">
          {{ $translate('project') }} {{ $translate('receipt.currency') }}
        </label>
      </div>

      <hr/>

      <!-- Make the logout button 100% wide by wrapping in a .btn-group.w-100 -->
      <div class="btn-group w-100">
        <button
            @click.stop.prevent="logout"
            type="submit"
            class="btn btn-secondary w-100"
        >
          <x-text-html locus="langpack.uiux.logout"></x-text-html>
        </button>
      </div>

    </div>
  </div>
</template>

<script>
import _Services from "@/x-html/_Services";
import state from "@/state";
import api from "@/api";

export default {
  mixins: [_Services],

  data: function () {
    return {
      cardFormVisible: false,
      changePlanVisible: false,
      doubleCancel: false,
      plans: [],
      plan: '',
      stripeActivated: false  // New flag to ensure stripe elements are activated only once
    }
  },

  computed: {
    paidPlans() {
      return (this.subscription.name.indexOf('Free') === -1)
          ? this.plans.slice(1)
          : this.plans;
    },
    headCount() {
      return this.$read('team').length;
    },
    showCardForm() {
      return this.cardFormVisible;
    },
    user() {
      return this.$read('user');
    },
    token() {
      return this.$read('user').token;
    },
    customer() {
      return this.$read('stripe');
    },
    subscription() {
      return this.$read('stripe').subscription;
    },
    // Dynamically build the Font Awesome class for the card brand
    cardBrandIcon() {
      if (!this.customer || !this.customer.card || !this.customer.card.brand) {
        return 'fa fa-credit-card';
      }
      return 'fa-brands fa-cc-'.concat(this.customer.card.brand.toLowerCase());
    },
    // Compute a CSS class based on the subscription status
    subscriptionStatusClass() {
      return 'badge-'.concat(this.customer?.subscription?.status?.toLowerCase());
    },
    numberCurrency: {
      get() {
        return stateTagApp.$read('langpack.numberCurrency');
      },
      set(val) {
        stateTagApp.$write('langpack.numberCurrency', val);
      }
    }
  },

  methods: {
    langChanged() {
      let url = endPoints.member.changeLanguage
      let language = stateTagApp.$read('langpack.selected');

      this.$read('ai').activated = 1;

      this.$api(url, 'UserLang', {language})
    },
    logout() {
      nukeEverything();
      //stateTagApp.commands.clear('user');
      //stateTagApp.commands.clear('project');
    },
    mightUpgrade() {
      this.changePlanVisible = true;
    },

    loadLicense() {
      if (_.isEmpty(this.token)) {
        return;
      }
      this.$api(endPoints.member.license, 'LicenseLimits')
          .then(response => {
            stateTagApp.commands.clear('errors.license');
            stateTagApp.$write('license', response.data);
          })
          .catch(error => {
            stateTagApp.commands.clear('license');
            stateTagApp.$write('errors.license', stateTagApp.$translate('no-license'));
          });
    },

    loadPlans() {
      if (_.isEmpty(this.token)) {
        return;
      }

      this.$api(endPoints.member.upgradeOptions, 'upgradeOptions')
          .then(response => {
            this.plans = response.data;
            this.plan = this.$read('stripe.subscription.stripe_id');
          })
          .catch(error => {
            // Handle error if needed
          });
    },

    cancelMembership() {
      this.$api(endPoints.member.cancelMembership, 'cancelMembership', undefined, 'delete')
          .then(response => {
            stateTagApp.commands.reset();
            alert(this.$translate('instructions.end-membership'));
          })
          .catch(error => {
            // Handle error if needed
          });
    },

    changePlan() {
      this.$api(endPoints.member.changePlan, 'changePlan', {plan: this.plan})
          .then(response => {
            stateTagApp.$write('stripe', response.data);
            this.plan = this.$read('stripe.subscription.stripe_id');
            this.changePlanVisible = false;
          })
          .catch(error => {
            stateTagApp.$write('errors.user', error.response.data.message);
          });
    },

    cancelChangePlan() {
      this.plan = this.$read('stripe.subscription.stripe_id');
      this.changePlanVisible = false;
    },

    updateCard() {
      stripeSubmitFunction()
          .then((token) => {
            this.$api(endPoints.member.updateCreditCard, 'StripeUpdate', {token})
                .then(response => {
                  stateTagApp.$write('stripe.card', response.data);
                  this.toggleCardForm();
                })
                .catch(error => {
                  // Handle error if needed
                });
          });
    },

    toggleCardForm() {
      // Activate stripe elements only once
      if (!this.stripeActivated) {
        activateStripeElements('#card-element');
        this.stripeActivated = true;
      }
      this.cardFormVisible = !this.cardFormVisible;
    },

    loadSubscription() {
      this.$api(endPoints.member.creditCard, 'Subscription')
          .then((response) => {
            this.$write('stripe', response.data);
          })
          .catch((err) => {
            console.error('Error fetching stripe data:', err);
          });
    }
  },

  watch: {
    user: {
      deep: true,
      handler: function (fresh) {
        // Optional: react to user changes
      }
    },
    headCount: {
      immediate: false,
      handler: function () {
        this.loadLicense();
      }
    },
    subscription: {
      deep: true,
      immediate: false,
      handler: function () {
        this.loadLicense();
      }
    },
    cardInputActivated(fresh, stale) {
      // Handle card input activation if necessary
    },
    token(fresh, stale) {
      if (!_.isEmpty(fresh)) {
        this.loadSubscription();
        this.loadLicense();
      }
    }
  },

  mounted() {
    this.loadPlans();
    this.loadLicense();
  }
}
</script>

