<template>
  <div class="dropdown d-inline-block" style="min-width: 0px !important;">
    <div
        :id="dropdownId"
        data-bs-toggle="dropdown"
        aria-expanded="false"
        style="cursor: pointer;"
        class="d-flex align-items-center p-2"
    >

      <i v-for="(icon, key) in iconMap"
         :key="key"
         class="fas"
         :class="[icon.icon, isActive(key) ? 'text-' + icon.color : 'icon-inactive', 'me-1']"
         :title="icon.title"
      ></i>

      <i class="fas fa-caret-down text-muted ms-1"></i>
    </div>
    <ul class="dropdown-menu dropdown-menu-sm dropdown-menu-end" :aria-labelledby="dropdownId">
      <li v-for="(icon, key) in iconMap" :key="key">
        <a
            class="dropdown-item"
            href="#"
            @click="handleClick(key)"
        >
          <i
              :class="['fas', icon.icon, isActive(key) ? 'text-' + icon.color : 'icon-inactive', 'me-2']"
          ></i>
          {{ $translate(icon.title) }}
        </a>
      </li>
    </ul>
  </div>
</template>

<script>
import _Services from "./_Services.vue";

export default {
  mixins: [_Services],

  props: {
    value: {
      type: String,
      default: () => [],
    },
    index: {
      type: Number,
      required: true,
    }
  },
  data() {
    return {
      iconMap: {
        irrelevant: {icon: "fa-ban", color: "irrelevant", title: "receipt.indicators.irrelevant"},
        excessive: {icon: "fa-exclamation-triangle", color: "excessive", title: "receipt.indicators.excessive"},
      },
      dropdownId: `dropdown-${Math.random().toString(36).substring(2, 10)}`,
    };
  },
  methods: {
    isActive(flag) {
      return this.value.includes(flag);
    },
    handleClick(flag) {
      this.$emit("select", {flag, index: this.index});
    },
  },
};
</script>

<style scoped>
.icon-inactive {
  color: #dedede !important;
}
</style>
