<template>
  <!-- Projects Dropup -->
  <div class="dropup">
    <button type="button" class="btn btn-light" id="projectsDropup" data-bs-toggle="dropdown" aria-expanded="false">
      <i class="fas fa-project-diagram fa-lg"></i>
    </button>
    <div class="dropdown-menu p-4 manage-dropdown" aria-labelledby="projectsDropup">
      <h5 class="dropdown-header">
        <x-text-html locus="langpack.uiux.projects"></x-text-html>
      </h5>

      <div class="error">
        <x-text-html locus="errors.projects"></x-text-html>
      </div>

      <ul v-if="projects.length > 0" class="list-unstyled mb-3">
        <li v-for="(project, index) in projects" :key="'project-' + index" class="dropdown-item-wrapper mb-2">
          <div class="menu-item-controls d-flex align-items-center">

            <x-input :locus="makeLocus(index)" max="30" type="text" name="project-name" icon="edit" @updated="handleUpdate"></x-input>

            <button v-show="!isActiveProject(project)" @click.prevent.stop="handleDelete(index)" type="button" class="btn btn-danger btn-sm delete-button">
              <i class="fas fa-trash-alt"></i>
            </button>

            <button v-show="isActiveProject(project)" @click.prevent.stop="() => {}" type="button" class="btn btn-light btn-sm delete-button" disabled>
              <i class="fas fa-check"></i>
            </button>

          </div>

          <button role="button" class="dropdown-item" @click.prevent="handleSelect(project)" :class="{ active: isActiveProject(project) }">
            <x-text-html :locus="makeLocus(index)"></x-text-html>
            <i v-show="isActiveProject(project)" class="fas fa-refresh ml-2" style="float: right;"></i>
          </button>

        </li>
      </ul>

      <hr>
      <div class="btn-group" role="group">
        <!-- Toggle text between Manage / Save -->
        <button
            :class="['btn', 'btn-secondary', 'roll-icon', 'manage-button', { active: manageMode }]"
            type="button"
        >
          <i class="fas fa-cog me-2"></i>
          <span v-if="!manageMode">
            <x-text-html locus="langpack.uiux.manage"></x-text-html>
          </span>
          <span v-else>
            <x-text-html locus="langpack.uiux.save"></x-text-html>
          </span>
        </button>

        <button
            @click="newProject"
            type="button"
            :class="['btn', 'btn-primary', 'roll-icon', 'add-button']"
        >
          <i class="fas fa-plus me-2"></i>
          <span><x-text-html locus="langpack.uiux.new"></x-text-html></span>
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import _Services from "@/x-html/_Services";

export default {
  mixins: [_Services],
  data() {
    return {
      manageMode: false
    };
  },
  computed: {
    index() {
      return index
    },
    projects() {
      return this.$read('projects') || [];
    },
    user() {
      return this.$read('user');
    },
    notification() {
      return stateTagApp.$read("notification");
    }
  },

  watch: {
    user: function (fresh, stale) {
      console.log('Token changed:', stale.token, '=>', fresh.token);
      if (!_.isEmpty(fresh.token)) {
        this.loadProjects();
      } else {
        stateTagApp.commands.clear('project');
        stateTagApp.commands.clear('projects');
      }
    },

    notification: {
      // deep: false,
      // immediate: false,
      handler: async function (fresh, stale) {
        //console.log('ran ProjectNav notification watcher')

        if(!fresh.id){
          return;
        }

        let project = stateTagApp.$read('project');
        let projects = stateTagApp.$read('projects') || [];

        let match = projects.find(p => p.id == fresh.project);

        if (_.isEmpty(match)) {
          await this.loadProjects();
          projects = stateTagApp.$read('projects')
          match = projects.find(p => p.id == fresh.project);
        }

        if (project.id != fresh.project) {
          stateTagApp.$write('project', match);
        }

        if(fresh.model == 'project'){
          stateTagApp.commands.clear('notification');
        }
      }
    },
  },

  mounted() {
    console.log('mounting project nav')
    this.attachManageModeEvents();
    if (!_.isEmpty(this.user.token)) {
      this.loadProjects();
    }
  },

  methods: {

    async loadProjects() {

      // Fetch initial projects data
      const endpoint = endPoints.projects.list;
      return this.$api(endpoint, 'ProjectsNav')
          .then((response) => {

            if (response.data.length == 0) {
              //this.newProject();
              return;
            }

            this.$write('projects', response.data);
            let project = this.$read('project');

            if (!_.isNumber(project.id) && !_.isEmpty(response.data)) {
              this.$write('project', response.data[0]);
            }

            if (response.data.length == 0) {
              alert(this.$translate('instructions.no-projects'));
            }

          })
          .catch((err) => {
            this.$write('projects', []);
            console.log(err);
          });
    },

    isActiveProject(project) {
      return (project.id == this.$read('project.id'))
          ? true
          : false;
    },

    handleSelect(project) {

      if (this.isActiveProject(project)) {

        // Fetch initial projects data
        const endpoint = endPoints.projects.list;
        this.$api(endpoint, 'ProjectsNav')
            .then((response) => {
              // @chatGPT We need to get a fresh copy of project from projects by matching project.id
              console.log(response)
              const fresh = response.data.find(p => p.id === project.id);
              console.log(fresh);
              if (fresh) {
                this.$write('project', fresh);
              }
            });
        return;
      }

      this.$write('project', project);
    },


    handleDelete(index) {

      let project = this.$read('projects[' + index + ']');
      let endpoint = endPoints.projects.delete.replace('{project}', project.id);

      if (project.id === this.$read('project.id')) {
        alert(this.$translate('errors.deleting-current-project'));
        return;
      }

      this.$api(endpoint, 'ProjectNavDeleteWithUndo', undefined, 'delete')
          .then((response) => {
            //stateTagApp.commands.clear('project');
            let projects = this.$read('projects');
            projects.splice(index, 1);
            this.$write('projects', projects);
          })
          .catch((error) => {
            //console.log(error)
          });
    },

    handleUpdate(event) {
      let locus = event.detail[0].locus.replace('.name', '');
      let project = this.$read(locus);
      let endpoint = endPoints.projects.update.replace('{project}', project.id);
      this.$api(endpoint, 'ProjectNavUpdate', project, 'PUT')
          .then((response) => {
            //console.log(response)
            let projects = this.$read('projects');
            if (project.id == this.$read('project.id')) {
              this.$write('project', project);
            }

          })
          .catch((response) => {
            //console.log(response)
          });
    },
    makeLocus(index) {
      return `projects.${index}.name`;
    },

    findUpdatedObject(freshObjects, staleObjects) {
      // Create a map (lookup) of stale objects by ID
      const staleMap = new Map(staleObjects.map(item => [item.id, item]));


      for (const freshItem of freshObjects) {
        const staleItem = staleMap.get(freshItem.id);
        if (staleItem && staleItem.value !== freshItem.value) {
          return freshItem;
        }
      }
      return null;
    },

    newProject() {
      // Just calls your existing method
      stateTagApp.commands.newProject();
    },
    attachManageModeEvents() {
      // Find the manage-button and toggle "manage-mode" on its dropdown
      const manageButton = this.$el.querySelector('.manage-button');
      if (manageButton) {
        manageButton.addEventListener('click', (e) => {
          e.stopPropagation();
          const dropdownMenu = manageButton.closest('.dropdown-menu');
          if (dropdownMenu) {
            dropdownMenu.classList.toggle('manage-mode');
            this.manageMode = !this.manageMode;
          }
        });
      }

      // Prevent dropdown from closing when clicking inside .menu-item-controls
      const menuControls = this.$el.querySelectorAll('.menu-item-controls');
      menuControls.forEach(control => {
        control.addEventListener('click', (e) => {
          e.stopPropagation();
        });
      });
    }
  },
};
</script>
