<template>
  <!-- Backdrop Fade Transition -->
  <div
      class="modal"
      :class="{ 'd-block': $read('nav.builder') }"
      v-show="$read('nav.builder')"
      style="background: rgba(0, 0, 0, 0.5); transition: opacity 0.5s ease;"
  >
    <!-- Slide Transition for Modal Content -->
    <transition name="modal-slide" appear>
      <div class="modal-dialog modal-slide" v-show="$read('nav.builder')">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">
              <x-text-html locus="project.name"></x-text-html>
            </h5>
<!--            <button type="button" class="btn btn-white" data-dismiss="modal" @click="$write('nav.builder', false)">
              <i class="fas fa-times"></i>
            </button>-->
          </div>

          <div class="error">
            <x-text-html locus="errors.builder"></x-text-html>
          </div>

          <!-- Added dynamic class for flex layout when adding text -->
          <div class="modal-body" :class="{ 'flex-col': isAddingText }">
            <p class="alert alert-info">{{ $translate('instructions.new-project') }}</p>

            <!-- Conditional Rendering: Show Assets or Input Field -->
            <div v-if="!isAddingText">
              <!-- Mocked Asset Items -->
              <div class="asset-item" v-for="(file, index) in files" :key="index">
                <button type="button" class="btn btn-danger btn-sm delete-button" @click="deleteAsset(index)">
                  <i class="fas fa-trash-alt"></i>
                </button>
                <i :class="file.iconClass" class="asset-icon"></i>
                <div class="asset-details">
                  <div class="asset-name">{{ file.name }}</div>
                  <div class="asset-type">{{ file.type }}</div>
                </div>
                <button type="button" class="btn btn-light">
                  <i :class="actionIcon(file.status)"></i>
                </button>
              </div>
            </div>

            <!-- Input Field for New Text -->
            <div v-else class="new-text-container">
              <textarea
                  type="text"
                  class="form-control mb-3"
                  placeholder="Enter your text here..."
                  v-model="newText"
              ></textarea>
            </div>
          </div>

          <!-- Modal Footer: Button Group or Save Button -->
          <div class="modal-footer d-flex align-items-center">
            <div v-if="!isAddingText" class="btn-group footer-btn-group mx-auto" role="group">
<!--              <button type="button" class="btn btn-light btn-lg" @click="$write('camera.currency', false); $write('nav.camera', cameraUrl)">
                <i class="bi bi-camera-fill"></i>
              </button>-->
              <button type="button" class="btn btn-light btn-lg" @click="$write('nav.microphone', microphoneUrl); $write('microphone.instruction', $translate('instructions.describe-project'));">
                <i class="bi bi-mic-fill"></i>
              </button>
              <button type="button" class="btn btn-light btn-lg" @click="triggerNewText">
                <i class="bi bi-file-earmark-text-fill"></i>
              </button>
            </div>

            <div v-else class="save-button-container mx-auto">
              <div class="btn-group" role="group">
                <button type="button" class="btn btn-light btn-lg" @click="cancelAction">
                  <i class="fas fa-cancel"></i>
                </button>
                <button type="button" class="btn btn-light btn-lg" @click="saveText" :disabled="!newText.trim()">
                  <i class="fas fa-save"></i>
                </button>
              </div>
            </div>

            <button
                v-if="!isAddingText"
                @click="processFiles"
                type="button"
                class="btn btn-lg ms-auto"
                :disabled="isProcessing">
              <i v-if="!isProcessing" class="fa fa-check"></i>
              <i v-else class="fa fa-spinner spin"></i>
            </button>
          </div>

        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import _Services from "./_Services.vue";

export default {
  mixins: [_Services],

  data() {
    return {
      isAddingText: false,
      newText: '',
      processingCount: 0,
    };
  },

  methods: {
    processFiles() {
      this.processingCount++;
      let url = endPoints.files.process.replace('{project}', this.project);
      this.$api(url, 'BuilderProcessFiles', {}, 'post')
          .then(response => {
            stateTagApp.$write('nav.builder', false);
            stateTagApp.commands.clear('builder');
            stateTagApp.$read('ai').activated = 1;;
          })
          .catch(error => {
            // Handle error if needed
          })
          .finally(() => {
            this.processingCount--;
          });
    },
    actionIcon(status) {
      switch (status) {
        case 'pending':
          return 'bi bi-clock';
        case 'processed':
          return 'bi bi-check';
      }
    },
    async loadProjectFiles() {
      this.processingCount++;

      if (!_.isNumber(stateTagApp.$read('project.id'))) {
        return;
      }

      let url = endPoints.files.list.replace('{project}', stateTagApp.$read('project.id'));
      return this.$api(url, 'Builder')
          .then((response) => {
            stateTagApp.$write('builder.files', response.data);
            //console.log('not processing');
          })
          .catch((error) => {
            // Optionally handle the error
          })
          .finally(() => {
            this.processingCount--;
          });
    },
    triggerNewText() {
      this.isAddingText = true;
    },
    cancelAction() {
      this.newText = '';
      this.isAddingText = false;
    },
    saveText() {
      this.processingCount++;
      this.isAddingText = false;

      const url = endPoints.files.store.replace('{project}', this.$read('project.id'));
      const blob = new Blob([this.newText.trim()], {type: 'text/plain'});
      const formData = new FormData();
      formData.append('file', blob, 'file.txt');

      const fileCount = this.$read('builder.files').length;
      this.$api(url, 'Builder', formData, undefined, {'Content-Type': 'multipart/form-data'})
          .then((response) => {
            this.newText = '';
            let maxTries = 5;
            const intervalId = setInterval(() => {
              this.loadProjectFiles()
                  .then(() => {
                    let fileCount2 = this.$read('builder.files').length;
                    if(fileCount != fileCount2 || maxTries-- <= 0){
                      clearInterval(intervalId);
                    }
                  })
            }, 1000);
            setTimeout(() => {
              this.loadProjectFiles();
            }, 1000);
            this.$broadcast({from: 'Project', type: 'api', event: 'project-updated', ...response.data});
          })
          .catch((error) => {
            console.error('Error saving text:', error);
            this.$write('errors.builder', this.$translate('errors.saving'));
          })
          .finally(() => {
            this.processingCount--;
          });
    },
    deleteAsset(index) {
      this.processingCount++;
      let filename = this.$read('builder.files['.concat(index).concat(']').concat('.name'));
      const url = endPoints.files.delete
          .replace('{project}', this.$read('project.id'))
          .replace('{file}', filename);
      this.$api(url, 'Builder', undefined, 'DELETE')
          .then((response) => {
            let files = this.$read('builder.files');
            files.splice(index, 1);
            this.$write('builder.files', files);
          })
          .catch((error) => {
            console.error('Error deleting asset:', error);
            this.$write('errors.builder', this.$translate('errors.deleting'));
          })
          .finally(() => {
            this.processingCount--;
          });
    },
    viewAsset(asset) {
      // Implement asset viewing logic here
    }
  },

  watch: {
    project(val, old) {
      this.loadProjectFiles();
    }
  },

  computed: {
    files() {
      return stateTagApp.$read('builder.files')
    },
    project() {
      return stateTagApp.$read('project.id');
    },
    isProcessing() {
      return this.processingCount > 0;
    },
    cameraUrl() {
      return endPoints.files.store.replace('{project}', stateTagApp.$read('project.id'));
    },
    microphoneUrl() {
      return endPoints.files.store.replace('{project}', stateTagApp.$read('project.id'));
    }
  }
};
</script>

