<template>
  <div class="dropup align-left">
    <button
        type="button"
        class="btn btn-light"
        id="teamsDropup"
        data-bs-toggle="dropdown"
        aria-expanded="false"
    >
      <i class="fas fa-users fa-lg"></i>
    </button>
    <div class="dropdown-menu p-4 manage-dropdown" aria-labelledby="teamsDropup">
      <h5 class="dropdown-header">{{ $translate('teams.project-team') }}</h5>

      <!-- Check if team has members -->
      <ul v-if="$read('team').length > 0" class="list-group mb-3 list-area">
        <!-- Iterate over team members -->
        <li
            v-for="(teammate, index) in $read('team')"
            :key="teammate.id || index"
            class="list-group-item d-flex justify-content-between align-items-center"
        >
          <!-- Teammate Information -->
          <div class="d-flex align-items-center">
            <!-- Optional: Add an avatar or icon -->
            <i class="fas fa-user-circle fa-2x me-3 text-primary"></i>
            <div>
              <h6 class="mb-0">{{ teammate.name }}</h6>
              <small class="text-muted">{{ teammate.email }}</small>
              <br/>
              <span class="badge bg-secondary">{{ teammate.role | translation }}</span>
            </div>
          </div>

        </li>
      </ul>

      <!-- No Team Members Message -->
      <p v-else class="text-muted">{{ $translate('teams.empty-team') }}}}</p>

      <hr/>

      <!-- Control Buttons -->
      <div class="d-flex justify-content-between">

        <button
            @click.prevent="$write('nav.team', true)"
            type="button"
            class="btn btn-primary d-flex align-items-center"
        >
          <i class="fas fa-cog me-2"></i>
          <span>{{ $translate('manage') }}</span>
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import _Services from "@/x-html/_Services";

export default {
  mixins: [_Services],

  computed: {
    projectId() {
      return this.$read('project.id');
    },

    canGrant() {
      for (const member of this.$read('team')) {
        if (member.me && member.role === 'grant') {
          return true;
        }
      }

      return false;
    }
  },

  methods: {

    loadTeam() {

      if (!this.projectId) {
        //console.log('No project selected', this.projectId);
        return;
      }

      let url = endPoints.projects.team
          .replace('{project}', this.projectId);

      this.$api(url, 'TeamNav')
          .then(response => {
            //console.log(response);
            stateTagApp.$write('team', response.data);
          })
          .catch((err)=>{});
    }
  },

  watch: {
    projectId: function (fresh, stale) {
      //console.log(fresh, stale);
      this.loadTeam();
    }
  },

  mounted() {
    this.loadTeam();
    this.$onSocket('ProjectTouched', function(event, payload){
      payload = (payload && payload.message) || {};
      if(payload.project == this.projectId) {
        this.loadTeam();
      }
    });
  }
}
</script>
<style scoped>
.list-area {
  max-height: 50vh;
  overflow-y: scroll;
}
</style>
