<template>
  <!-- Form with "ref" for reference -->
  <form ref="theForm"
        class="needs-validation text-center"
        novalidate
        @submit.prevent="validateAndSubmitForm($event, submitForm)">
    <!-- Email Input -->
    <div class="mb-3">
      <h4 class="d-flex justify-content-center align-items-center">
        <i class="fas fa-user me-2"></i>
        <x-text-html locus="user.email"></x-text-html>
      </h4>
    </div>

    <!-- Password Input Fields -->
    <div class="mb-3">
      <div class="input-group">
    <span class="input-group-text">
      <i class="fa fa-lock"></i>
    </span>
        <input
            name="password"
            type="password"
            class="form-control"
            required>
      </div>
    </div>

    <div class="mb-3">
      <div class="input-group">
    <span class="input-group-text">
      <i class="fa fa-lock"></i>
    </span>
        <input
            name="password_confirmation"
            type="password"
            class="form-control"
            required>
      </div>
    </div>


    <!-- Submit Button -->
    <div class="d-flex justify-content-center">
      <div class="btn-group">
        <button type="button" @click.prevent.stop="$write('nav.passwordSet', false)" class="btn btn-secondary">
          {{ $translate('cancel') }}
        </button>
        <button  type="submit" class="btn btn-primary">
          {{ $translate('submit') }}
        </button>
      </div>
    </div>
  </form>
</template>

<script>
import _Services from "@/x-html/_Services";
import _FormValidation from "@/x-html/_FormValidation";

export default {
  mixins: [_Services, _FormValidation],
  methods: {
    submitForm() {
      this.$api(endPoints.password.set, 'SetPassword', json)
          .then(response => {
            stateTagApp.commands.clear('nav.passwordSet');
            stateTagApp.commands.clearModal();
            this.$write("user", response.data);
          })
          .catch(error => {
            if (error.response && error.response.data.errors) {
              let errors = error.response.data.errors;
              let errorMessages = Object.values(errors).flat();
              let formattedErrors = errorMessages.map(msg => `- ${msg}`).join("\n");
              stateTagApp.$write('errors.modal', formattedErrors);
            } else {
              stateTagApp.$write('errors.modal', "- An unknown error occurred.");
            }
          });
    }
  }
};
</script>
